// src/ui/HUD.ts
/**
 * HUD (Heads-Up Display) principal du jeu.
 * 
 * Responsabilits:
 * - Afficher les ressources du joueur
 * - Afficher les units slectionnes
 * - Afficher la minimap tactique
 * - Afficher les notifications
 * - Menu de construction
 * - Ordres et formations
 * 
 * Architecture: DOM simple, pas de framework UI lourd.
 * 
 * KISS: Structure HTML/CSS inline, manipulation DOM directe.
 */

import type { EntityId, PlayerId } from '../types/commands';
import type { UnitType, Resources, PlayerState } from '../types/GameState';
import { eventBus } from '../core/EventBus';

// ============================================================================
// Configuration
// ============================================================================

export const HUD_CONFIG = {
  /** Dure d'affichage des notifications (ms) */
  notificationDuration: 3000,
  
  /** Nombre max de notifications affiches */
  maxNotifications: 5,
  
  /** Intervalle de mise  jour du HUD (ms) */
  updateInterval: 100,
  
  /** Taille de la minimap (px) */
  minimapSize: 200,
};

// ============================================================================
// Types
// ============================================================================

export type NotificationType = 'INFO' | 'WARNING' | 'ERROR' | 'SUCCESS' | 'COMBAT';

export interface Notification {
  id: string;
  type: NotificationType;
  message: string;
  timestamp: number;
}

export interface SelectedUnitInfo {
  id: EntityId;
  type: UnitType;
  name: string;
  health: number;
  maxHealth: number;
  shield?: number;
  maxShield?: number;
}

export interface HUDCallbacks {
  onBuildUnit?: (unitType: UnitType) => void;
  onFormation?: (type: string) => void;
  onOrderAttack?: () => void;
  onOrderMove?: () => void;
  onOrderStop?: () => void;
  onOrderPatrol?: () => void;
  onGroupCreate?: (groupIndex: number) => void;
  onGroupSelect?: (groupIndex: number) => void;
  onMinimapClick?: (x: number, y: number) => void;
}

