// ============================================================================
// Classe HUD
// ============================================================================

/**
 * HUD principal du jeu.
 */
export class HUD {
  private container: HTMLDivElement;
  private styleElement: HTMLStyleElement;
  
  // lments du HUD
  private topbar: HTMLDivElement | null = null;
  private selectionPanel: HTMLDivElement | null = null;
  private unitInfoPanel: HTMLDivElement | null = null;
  private ordersPanel: HTMLDivElement | null = null;
  private minimap: HTMLDivElement | null = null;
  private minimapCanvas: HTMLCanvasElement | null = null;
  private minimapCtx: CanvasRenderingContext2D | null = null;
  private notificationsContainer: HTMLDivElement | null = null;
  private buildMenu: HTMLDivElement | null = null;
  private controlGroups: HTMLDivElement | null = null;
  private formationsPanel: HTMLDivElement | null = null;
  private fpsDisplay: HTMLDivElement | null = null;
  private gameTimeDisplay: HTMLDivElement | null = null;
  
  // tat
  private resources: Resources = {
    credits: 1000,
    metal: 500,
    crystal: 100,
    fuel: 200,
    population: 0
  };
  
  private selectedUnits: SelectedUnitInfo[] = [];
  private notifications: Notification[] = [];
  private controlGroupsData: Map<number, EntityId[]> = new Map();
  private callbacks: HUDCallbacks = {};
  private lastFrameTime: number = 0;
  private frameCount: number = 0;
  private currentFPS: number = 60;
  private gameStartTime: number = Date.now();
  private buildMenuVisible: boolean = false;
  
  constructor() {
    // Crer le conteneur principal
    this.container = document.createElement('div');
    this.container.className = 'hud-container';
    
    // Ajouter les styles
    this.styleElement = document.createElement('style');
    this.styleElement.textContent = HUD_STYLES;
    document.head.appendChild(this.styleElement);
    
    // Construire le HUD
    this.build();
    
    // Ajouter au DOM
    document.body.appendChild(this.container);
  }
  
  // ============================================================================
  // Construction du HUD
  // ============================================================================
  
  private build(): void {
    this.buildTopbar();
    this.buildBottomPanel();
    this.buildMinimap();
    this.buildNotifications();
    this.buildBuildMenu();
    this.buildControlGroups();
    this.buildFormations();
    this.buildFPSDisplay();
    this.buildGameTimeDisplay();
  }
  
  private buildTopbar(): void {
    this.topbar = document.createElement('div');
    this.topbar.className = 'hud-topbar';
    this.topbar.innerHTML = `
      <div class="resource-item">
        <div class="resource-icon"></div>
        <div>
          <div class="resource-value" id="res-credits">1000</div>
          <div class="resource-label">Crdits</div>
        </div>
      </div>
      <div class="resource-item">
        <div class="resource-icon"></div>
        <div>
          <div class="resource-value" id="res-metal">500</div>
          <div class="resource-label">Mtal</div>
        </div>
      </div>
      <div class="resource-item">
        <div class="resource-icon"></div>
        <div>
          <div class="resource-value" id="res-crystal">100</div>
          <div class="resource-label">Cristaux</div>
        </div>
      </div>
      <div class="resource-item">
        <div class="resource-icon"></div>
        <div>
          <div class="resource-value" id="res-fuel">200</div>
          <div class="resource-label">Carburant</div>
        </div>
      </div>
      <div class="resource-item">
        <div class="resource-icon"></div>
        <div>
          <div class="resource-value" id="res-population">0/100</div>
          <div class="resource-label">Population</div>
        </div>
      </div>
    `;
    this.container.appendChild(this.topbar);
  }
  
  private buildBottomPanel(): void {
    const bottom = document.createElement('div');
    bottom.className = 'hud-bottom';
    
    // Panel de slection
    this.selectionPanel = document.createElement('div');
    this.selectionPanel.className = 'selection-panel';
    this.selectionPanel.id = 'selection-panel';
    bottom.appendChild(this.selectionPanel);
    
    // Info panel
    this.unitInfoPanel = document.createElement('div');
    this.unitInfoPanel.className = 'unit-info-panel';
    this.unitInfoPanel.id = 'unit-info-panel';
    this.unitInfoPanel.innerHTML = `
      <div class="unit-info-name">Aucune slection</div>
      <div class="unit-info-type">-</div>
    `;
    bottom.appendChild(this.unitInfoPanel);
    
    // Orders panel
    this.ordersPanel = document.createElement('div');
    this.ordersPanel.className = 'orders-panel';
    this.ordersPanel.innerHTML = `
      <button class="order-button" id="order-move" title="Dplacer (M)">
        <span class="icon"></span>
        <span>Move</span>
      </button>
      <button class="order-button" id="order-attack" title="Attaquer (A)">
        <span class="icon"></span>
        <span>Attack</span>
      </button>
      <button class="order-button" id="order-stop" title="Stop (S)">
        <span class="icon"></span>
        <span>Stop</span>
      </button>
      <button class="order-button" id="order-patrol" title="Patrouiller (P)">
        <span class="icon"></span>
        <span>Patrol</span>
      </button>
      <button class="order-button" id="order-defend" title="Dfendre (D)">
        <span class="icon"></span>
        <span>Defend</span>
      </button>
      <button class="order-button" id="order-build" title="Construire (B)">
        <span class="icon"></span>
        <span>Build</span>
      </button>
    `;
    bottom.appendChild(this.ordersPanel);
    
    // Event listeners pour les ordres
    this.ordersPanel.querySelector('#order-move')?.addEventListener('click', () => {
      this.callbacks.onOrderMove?.();
    });
    this.ordersPanel.querySelector('#order-attack')?.addEventListener('click', () => {
      this.callbacks.onOrderAttack?.();
    });
    this.ordersPanel.querySelector('#order-stop')?.addEventListener('click', () => {
      this.callbacks.onOrderStop?.();
    });
    this.ordersPanel.querySelector('#order-patrol')?.addEventListener('click', () => {
      this.callbacks.onOrderPatrol?.();
    });
    this.ordersPanel.querySelector('#order-build')?.addEventListener('click', () => {
      this.toggleBuildMenu();
    });
    
    this.container.appendChild(bottom);
  }
  
  private buildMinimap(): void {
    this.minimap = document.createElement('div');
    this.minimap.className = 'hud-minimap';
    
    this.minimapCanvas = document.createElement('canvas');
    this.minimapCanvas.className = 'minimap-canvas';
    this.minimapCanvas.width = HUD_CONFIG.minimapSize;
    this.minimapCanvas.height = HUD_CONFIG.minimapSize;
    this.minimapCtx = this.minimapCanvas.getContext('2d');
    
    this.minimap.appendChild(this.minimapCanvas);
    
    // Viewport indicator
    const viewport = document.createElement('div');
    viewport.className = 'minimap-viewport';
    viewport.id = 'minimap-viewport';
    this.minimap.appendChild(viewport);
    
    // Click handler
    this.minimapCanvas.addEventListener('click', (e) => {
      const rect = this.minimapCanvas!.getBoundingClientRect();
      const x = (e.clientX - rect.left) / rect.width;
      const y = (e.clientY - rect.top) / rect.height;
      this.callbacks.onMinimapClick?.(x, y);
    });
    
    this.container.appendChild(this.minimap);
  }
  
  private buildNotifications(): void {
    this.notificationsContainer = document.createElement('div');
    this.notificationsContainer.className = 'hud-notifications';
    this.notificationsContainer.id = 'notifications';
    this.container.appendChild(this.notificationsContainer);
  }
  
  private buildBuildMenu(): void {
    this.buildMenu = document.createElement('div');
    this.buildMenu.className = 'hud-build-menu';
    this.buildMenu.innerHTML = `
      <div class="build-menu-header">Construction</div>
      <div class="build-menu-items">
        <div class="build-item" data-unit="FIGHTER">
          <span class="icon"></span>
          <span class="cost">100</span>
        </div>
        <div class="build-item" data-unit="CORVETTE">
          <span class="icon"></span>
          <span class="cost">250</span>
        </div>
        <div class="build-item" data-unit="FRIGATE">
          <span class="icon"></span>
          <span class="cost">500</span>
        </div>
        <div class="build-item" data-unit="DESTROYER">
          <span class="icon"></span>
          <span class="cost">800</span>
        </div>
        <div class="build-item" data-unit="CRUISER">
          <span class="icon"></span>
          <span class="cost">1200</span>
        </div>
        <div class="build-item" data-unit="HARVESTER">
          <span class="icon"></span>
          <span class="cost">300</span>
        </div>
        <div class="build-item" data-unit="REPAIR">
          <span class="icon"></span>
          <span class="cost">400</span>
        </div>
        <div class="build-item disabled" data-unit="MOTHERSHIP">
          <span class="icon"></span>
          <span class="cost">5000</span>
        </div>
      </div>
    `;
    
    // Event listeners pour les items de construction
    this.buildMenu.querySelectorAll('.build-item').forEach(item => {
      item.addEventListener('click', () => {
        const unitType = item.getAttribute('data-unit') as UnitType;
        if (!item.classList.contains('disabled')) {
          this.callbacks.onBuildUnit?.(unitType);
          this.addNotification('INFO', `Construction de ${unitType} lance`);
        }
      });
    });
    
    this.container.appendChild(this.buildMenu);
  }
  
  private buildControlGroups(): void {
    this.controlGroups = document.createElement('div');
    this.controlGroups.className = 'hud-control-groups';
    
    for (let i = 1; i <= 9; i++) {
      const group = document.createElement('div');
      group.className = 'control-group';
      group.dataset.group = String(i);
      group.innerHTML = `
        <span>${i}</span>
        <span class="count">0</span>
      `;
      
      group.addEventListener('click', () => {
        this.callbacks.onGroupSelect?.(i);
      });
      
      group.addEventListener('dblclick', () => {
        this.callbacks.onGroupCreate?.(i);
      });
      
      this.controlGroups.appendChild(group);
    }
    
    this.container.appendChild(this.controlGroups);
  }
  
  private buildFormations(): void {
    this.formationsPanel = document.createElement('div');
    this.formationsPanel.className = 'hud-formations';
    this.formationsPanel.innerHTML = `
      <button class="formation-button" data-formation="WEDGE" title="Formation V">
        <span class="icon"></span>
        <span>Wedge</span>
      </button>
      <button class="formation-button" data-formation="SPHERE" title="Formation Sphre">
        <span class="icon"></span>
        <span>Sphere</span>
      </button>
      <button class="formation-button" data-formation="WALL" title="Formation Mur">
        <span class="icon"></span>
        <span>Wall</span>
      </button>
      <button class="formation-button" data-formation="CLAW" title="Formation Pince">
        <span class="icon"></span>
        <span>Claw</span>
      </button>
      <button class="formation-button" data-formation="COLUMN" title="Formation Colonne">
        <span class="icon"></span>
        <span>Column</span>
      </button>
    `;
    
    this.formationsPanel.querySelectorAll('.formation-button').forEach(btn => {
      btn.addEventListener('click', () => {
        const formation = btn.getAttribute('data-formation');
        if (formation) {
          this.callbacks.onFormation?.(formation);
          this.addNotification('INFO', `Formation: ${formation}`);
        }
      });
    });
    
    this.container.appendChild(this.formationsPanel);
  }
  
  private buildFPSDisplay(): void {
    this.fpsDisplay = document.createElement('div');
    this.fpsDisplay.className = 'hud-fps';
    this.fpsDisplay.textContent = 'FPS: 60';
    this.container.appendChild(this.fpsDisplay);
  }
  
  private buildGameTimeDisplay(): void {
    this.gameTimeDisplay = document.createElement('div');
    this.gameTimeDisplay.className = 'hud-game-time';
    this.gameTimeDisplay.textContent = '00:00:00';
    this.container.appendChild(this.gameTimeDisplay);
  }
  
  // ============================================================================
  // API publique
  // ============================================================================
  
  /**
   * Configure les callbacks.
   */
  setCallbacks(callbacks: HUDCallbacks): void {
    this.callbacks = { ...this.callbacks, ...callbacks };
  }
  
  /**
   * Met  jour les ressources affiches.
   */
  updateResources(resources: Resources): void {
    this.resources = resources;
    
    const credits = document.getElementById('res-credits');
    const metal = document.getElementById('res-metal');
    const crystal = document.getElementById('res-crystal');
    const fuel = document.getElementById('res-fuel');
    const population = document.getElementById('res-population');
    
    if (credits) credits.textContent = String(resources.credits);
    if (metal) metal.textContent = String(resources.metal);
    if (crystal) crystal.textContent = String(resources.crystal);
    if (fuel) fuel.textContent = String(resources.fuel);
    if (population) population.textContent = `${resources.population}/100`;
  }
  
  /**
   * Met  jour les units slectionnes.
   */
  updateSelection(units: SelectedUnitInfo[]): void {
    this.selectedUnits = units;
    
    if (!this.selectionPanel || !this.unitInfoPanel) return;
    
    // Vider le panel de slection
    this.selectionPanel.innerHTML = '';
    
    // Ajouter les units slectionnes
    units.slice(0, 15).forEach((unit, index) => {
      const el = document.createElement('div');
      el.className = `selected-unit ${index === 0 ? 'primary' : ''}`;
      
      const healthPercent = unit.health / unit.maxHealth;
      let healthClass = '';
      if (healthPercent <= 0.25) healthClass = 'critical';
      else if (healthPercent <= 0.5) healthClass = 'low';
      
      el.innerHTML = `
        <span class="unit-icon">${this.getUnitIcon(unit.type)}</span>
        <div class="unit-health-bar">
          <div class="unit-health-fill ${healthClass}" style="width: ${healthPercent * 100}%"></div>
        </div>
      `;
      
      this.selectionPanel!.appendChild(el);
    });
    
    // Mettre  jour le panel d'info
    if (units.length > 0) {
      const primary = units[0];
      this.unitInfoPanel.innerHTML = `
        <div class="unit-info-name">${primary.name}</div>
        <div class="unit-info-type">${primary.type}</div>
        <div class="unit-stat">
          <span class="unit-stat-label">Sant</span>
          <span>${primary.health}/${primary.maxHealth}</span>
        </div>
        ${primary.shield !== undefined ? `
          <div class="unit-stat">
            <span class="unit-stat-label">Bouclier</span>
            <span>${primary.shield}/${primary.maxShield}</span>
          </div>
        ` : ''}
        ${units.length > 1 ? `
          <div class="unit-stat">
            <span class="unit-stat-label">Total slectionn</span>
            <span>${units.length} units</span>
          </div>
        ` : ''}
      `;
    } else {
      this.unitInfoPanel.innerHTML = `
        <div class="unit-info-name">Aucune slection</div>
        <div class="unit-info-type">-</div>
      `;
    }
  }
  
  /**
   * Ajoute une notification.
   */
  addNotification(type: NotificationType, message: string): void {
    const id = `notif-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    
    const notification: Notification = {
      id,
      type,
      message,
      timestamp: Date.now()
    };
    
    this.notifications.push(notification);
    
    // Limiter le nombre de notifications
    while (this.notifications.length > HUD_CONFIG.maxNotifications) {
      const removed = this.notifications.shift();
      if (removed) {
        document.getElementById(removed.id)?.remove();
      }
    }
    
    // Afficher la notification
    if (this.notificationsContainer) {
      const el = document.createElement('div');
      el.id = id;
      el.className = `notification ${type}`;
      el.textContent = message;
      this.notificationsContainer.appendChild(el);
      
      // Supprimer aprs le dlai
      setTimeout(() => {
        el.remove();
        this.notifications = this.notifications.filter(n => n.id !== id);
      }, HUD_CONFIG.notificationDuration);
    }
  }
  
  /**
   * Met  jour la minimap.
   */
  updateMinimap(
    entities: Array<{ x: number; z: number; type: 'friendly' | 'enemy' | 'neutral' | 'resource' }>,
    viewportX: number,
    viewportZ: number,
    viewportWidth: number,
    viewportHeight: number
  ): void {
    if (!this.minimapCtx || !this.minimapCanvas) return;
    
    const ctx = this.minimapCtx;
    const size = HUD_CONFIG.minimapSize;
    
    // Clear
    ctx.fillStyle = 'rgba(0, 30, 60, 0.9)';
    ctx.fillRect(0, 0, size, size);
    
    // Grid
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.1)';
    ctx.lineWidth = 1;
    for (let i = 0; i < 10; i++) {
      const pos = (i / 10) * size;
      ctx.beginPath();
      ctx.moveTo(pos, 0);
      ctx.lineTo(pos, size);
      ctx.stroke();
      ctx.beginPath();
      ctx.moveTo(0, pos);
      ctx.lineTo(size, pos);
      ctx.stroke();
    }
    
    // Entities
    for (const entity of entities) {
      const x = ((entity.x + 500) / 1000) * size; // Normaliser
      const z = ((entity.z + 500) / 1000) * size;
      
      switch (entity.type) {
        case 'friendly':
          ctx.fillStyle = '#4caf50';
          break;
        case 'enemy':
          ctx.fillStyle = '#f44336';
          break;
        case 'neutral':
          ctx.fillStyle = '#9e9e9e';
          break;
        case 'resource':
          ctx.fillStyle = '#ffeb3b';
          break;
      }
      
      ctx.beginPath();
      ctx.arc(x, z, 3, 0, Math.PI * 2);
      ctx.fill();
    }
    
    // Viewport
    const viewport = document.getElementById('minimap-viewport');
    if (viewport) {
      const vpX = ((viewportX + 500) / 1000) * size;
      const vpZ = ((viewportZ + 500) / 1000) * size;
      const vpW = (viewportWidth / 1000) * size;
      const vpH = (viewportHeight / 1000) * size;
      
      viewport.style.left = `${vpX}px`;
      viewport.style.top = `${vpZ}px`;
      viewport.style.width = `${vpW}px`;
      viewport.style.height = `${vpH}px`;
    }
  }
  
  /**
   * Met  jour le groupe de contrle.
   */
  updateControlGroup(groupIndex: number, unitIds: EntityId[]): void {
    this.controlGroupsData.set(groupIndex, unitIds);
    
    const group = this.controlGroups?.querySelector(`[data-group="${groupIndex}"]`);
    if (group) {
      const countEl = group.querySelector('.count');
      if (countEl) {
        countEl.textContent = String(unitIds.length);
      }
      
      if (unitIds.length > 0) {
        group.classList.add('active');
      } else {
        group.classList.remove('active');
      }
    }
  }
  
  /**
   * Met  jour le FPS.
   */
  updateFPS(): void {
    const now = performance.now();
    this.frameCount++;
    
    if (now - this.lastFrameTime >= 1000) {
      this.currentFPS = this.frameCount;
      this.frameCount = 0;
      this.lastFrameTime = now;
      
      if (this.fpsDisplay) {
        this.fpsDisplay.textContent = `FPS: ${this.currentFPS}`;
      }
    }
  }
  
  /**
   * Met  jour le temps de jeu.
   */
  updateGameTime(): void {
    const elapsed = Date.now() - this.gameStartTime;
    const hours = Math.floor(elapsed / 3600000);
    const minutes = Math.floor((elapsed % 3600000) / 60000);
    const seconds = Math.floor((elapsed % 60000) / 1000);
    
    if (this.gameTimeDisplay) {
      this.gameTimeDisplay.textContent = 
        `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
    }
  }
  
  /**
   * Toggle le menu de construction.
   */
  toggleBuildMenu(): void {
    this.buildMenuVisible = !this.buildMenuVisible;
    this.buildMenu?.classList.toggle('visible', this.buildMenuVisible);
  }
  
  /**
   * Affiche/masque le HUD.
   */
  setVisible(visible: boolean): void {
    this.container.style.display = visible ? 'block' : 'none';
  }
  
  /**
   * Connecte le HUD au systme d'vnements pour mise  jour automatique.
   * S'abonne aux vnements de l'EventBus.
   */
  connectToEventBus(): void {
    // Ressources changes
    eventBus.on('resources:changed', (data) => {
      this.updateResources(data.resources as any);
    });
    
    // Slection change
    eventBus.on('selection:changed', (data) => {
      // Convertir les IDs en infos d'unit (simplifi)
      const units: SelectedUnitInfo[] = data.unitIds.map((id, _idx) => ({
        id,
        type: 'FIGHTER' as any, //  rcuprer du GameState
        name: `Unit ${id}`,
        health: 100,
        maxHealth: 100,
        shield: 50,
        maxShield: 50
      }));
      this.updateSelection(units);
    });
    
    // Slection efface
    eventBus.on('selection:cleared', () => {
      this.updateSelection([]);
    });
    
    // Notifications UI
    eventBus.on('ui:notification', (data) => {
      this.showNotification(data.type.toUpperCase() as any, data.message);
    });
    
    // Unit dtruite -> notification
    eventBus.on('unit:destroyed', (data) => {
      this.showNotification('COMBAT', `Unit ${data.unitId} dtruite!`);
    });
    
    // Combat dmarr
    eventBus.on('combat:started', (_data) => {
      this.showNotification('COMBAT', 'Combat engag!');
    });
    
    // Partie dmarre
    eventBus.on('game:start', (data) => {
      this.showNotification('SUCCESS', `Partie dmarre! Joueur: ${data.playerName}`);
      this.gameStartTime = Date.now();
    });
    
    // Joueur rejoint
    eventBus.on('net:player-joined', (data) => {
      this.showNotification('INFO', `${data.playerName} a rejoint la partie`);
    });
    
    // Joueur parti
    eventBus.on('net:player-left', (data) => {
      this.showNotification('WARNING', `${data.playerId} a quitt la partie`);
    });
    
    console.log('[HUD] Connected to EventBus');
  }
  
  /**
   * Rinitialise le temps de jeu.
   */
  resetGameTime(): void {
    this.gameStartTime = Date.now();
  }
  
  // ============================================================================
  // Utilitaires
  // ============================================================================
  
  private getUnitIcon(type: UnitType): string {
    const icons: Record<UnitType, string> = {
      FIGHTER: '',
      CORVETTE: '',
      FRIGATE: '',
      DESTROYER: '',
      CRUISER: '',
      MOTHERSHIP: '',
      HARVESTER: '',
      REPAIR: '',
      RESEARCH: ''
    };
    return icons[type] || '';
  }
  
  // ============================================================================
  // Nettoyage
  // ============================================================================
  
  /**
   * Dtruit le HUD.
   */
  dispose(): void {
    this.container.remove();
    this.styleElement.remove();
  }
}

// ============================================================================
// Singleton export
// ============================================================================

/** Instance globale du HUD */
export const hud = new HUD();