// src/game/GameIntegration.ts
/**
 * Intgration complte du jeu - Point d'entre de haut niveau.
 * 
 * Responsabilits:
 * - Initialiser tous les systmes de jeu
 * - Connecter les vnements entre systmes
 * - Grer le dmarrage/arrt de partie
 * - Coordonner le multijoueur avec la simulation locale
 * 
 * KISS: Faade simple pour orchestrer les systmes complexes.
 */

import * as THREE from 'three';
import { eventBus } from '../core/EventBus';
import { GameManager } from './GameManager';
import { GameLoop, GAME_LOOP_CONFIG } from './GameLoop';
import { CommandProcessor, processCommand } from './CommandProcessor';
import { UnitController } from './units/UnitController';
import { FormationManager } from '../ai/FormationManager';
import { p2pManager } from '../net/P2PManager';
import { GalaxyGenerator, type GalaxyData, type SystemData } from '../universe/GalaxyGenerator';
import type { GameState, Player, ShipClass } from '../types/GameState';
import type { GameCommand, PlayerId, Position3D, EntityId } from '../types/commands';
import { spawnUnitFree } from './units/UnitFactory';

// ============================================================================
// Configuration
// ============================================================================

export interface GameIntegrationConfig {
    /** Nom du joueur local */
    playerName: string;
    
    /** Seed de la galaxie (si host) */
    galaxySeed?: number;
    
    /** Nombre de clusters X */
    clustersX?: number;
    
    /** Nombre de clusters Z */
    clustersZ?: number;
    
    /** Mode debug */
    debug?: boolean;
}

export interface GameSession {
    /** ID de session/room */
    sessionId: string;
    
    /** Est-ce que ce client est l'hte */
    isHost: boolean;
    
    /** ID du joueur local */
    localPlayerId: PlayerId;
    
    /** Donnes de la galaxie gnre */
    galaxy: GalaxyData;
    
    /** tat de la partie */
    state: 'lobby' | 'starting' | 'playing' | 'paused' | 'ended';
}

// ============================================================================
// Classe GameIntegration
// ============================================================================

/**
 * Classe principale d'intgration qui lie tous les systmes.
 */
export class GameIntegration {
    // Managers de jeu
    private gameManager: GameManager;
    private gameLoop: GameLoop;
    private commandProcessor: CommandProcessor;
    private unitController: UnitController | null = null;
    private formationManager: FormationManager;
    private galaxyGenerator: GalaxyGenerator;
    
    // Session courante
    private session: GameSession | null = null;
    
    // Configuration
    private config: GameIntegrationConfig;
    
    // Callbacks UI
    private onSessionUpdate: ((session: GameSession) => void) | null = null;
    private onError: ((error: Error) => void) | null = null;
    
    constructor(config: GameIntegrationConfig) {
        this.config = {
            clustersX: 3,
            clustersZ: 3,
            debug: false,
            ...config
        };
        
        // Initialiser les managers
        this.gameManager = GameManager.getInstance();
        this.gameLoop = new GameLoop();
        this.commandProcessor = new CommandProcessor();
        this.formationManager = new FormationManager();
        this.galaxyGenerator = new GalaxyGenerator();
        
        // Configurer les callbacks rseau
        this.setupNetworkCallbacks();
        
        // Configurer les vnements de jeu
        this.setupGameEvents();
        
        console.log('[GameIntegration] Initialized');
    }
    
    // ============================================================================
    // Configuration
    // ============================================================================
    
    /**
     * Dfinit les callbacks UI.
     */
    setCallbacks(callbacks: {
        onSessionUpdate?: (session: GameSession) => void;
        onError?: (error: Error) => void;
    }): void {
        if (callbacks.onSessionUpdate) this.onSessionUpdate = callbacks.onSessionUpdate;
        if (callbacks.onError) this.onError = callbacks.onError;
    }
    
    /**
     * Configure les callbacks rseau.
     */
    private setupNetworkCallbacks(): void {
        p2pManager.setCallbacks({
            onConnected: (peerId) => {
                console.log(`[GameIntegration] Connected as ${peerId}`);
            },
            
            onPeerJoined: (peer) => {
                console.log(`[GameIntegration] Player joined: ${peer.playerName}`);
                eventBus.emit('net:player-joined', {
                    playerId: peer.playerId,
                    playerName: peer.playerName
                });
            },
            
            onPeerLeft: (peer) => {
                console.log(`[GameIntegration] Player left: ${peer.playerName}`);
                eventBus.emit('net:player-left', { playerId: peer.playerId });
            },
            
            onCommand: (command, senderId) => {
                this.handleRemoteCommand(command, senderId);
            },
            
            onSnapshot: (state, frameNumber) => {
                this.handleSnapshot(state as Partial<GameState>, frameNumber);
            },
            
            onGameStart: () => {
                this.handleGameStart();
            },
            
            onGameEnd: () => {
                this.handleGameEnd();
            },
            
            onError: (error) => {
                console.error('[GameIntegration] Network error:', error);
                this.onError?.(error);
            }
        });
    }
    
    /**
     * Configure les vnements de jeu.
     */
    private setupGameEvents(): void {
        // Unit dtruite -> notification UI
        eventBus.on('unit:destroyed', (data) => {
            eventBus.emit('ui:notification', {
                type: 'warning',
                message: `Unit ${data.unitId} destroyed`
            });
        });
        
        // Combat dmarr -> son/effet
        eventBus.on('combat:started', (_data) => {
            // TODO: Jouer son de combat
        });
        
        // Ressources changes -> mise  jour HUD
        eventBus.on('resources:changed', (_data) => {
            // Le HUD coute cet vnement directement
        });
    }
    
    // ============================================================================
    // Session Management
    // ============================================================================
    
    /**
     * Cre une nouvelle partie (mode host).
     */
    async hostGame(roomId?: string): Promise<GameSession> {
        console.log('[GameIntegration] Hosting new game...');
        
        // Se connecter au serveur PeerJS
        const connected = await p2pManager.connect();
        if (!connected) {
            throw new Error('Failed to connect to signaling server');
        }
        
        // Crer la room
        const room = p2pManager.createRoom(roomId);
        
        // Gnrer la galaxie
        const seed = this.config.galaxySeed ?? Date.now();
        const galaxy = this.galaxyGenerator.generateGalaxy(
            seed,
            `Galaxy-${seed}`,
            this.config.clustersX!,
            this.config.clustersZ!
        );
        
        // Initialiser le GameLoop
        const localPlayerId = p2pManager.getLocalPlayerId();
        this.gameLoop.initializeGame(localPlayerId, this.config.playerName);
        
        // Crer l'UnitController avec le state
        const gameState = this.gameLoop.getGameState();
        this.unitController = new UnitController(gameState);
        
        // Crer la session
        this.session = {
            sessionId: room.roomId,
            isHost: true,
            localPlayerId,
            galaxy,
            state: 'lobby'
        };
        
        this.onSessionUpdate?.(this.session);
        
        console.log(`[GameIntegration] Game hosted: ${room.roomId}`);
        return this.session;
    }
    
    /**
     * Rejoint une partie existante.
     */
    async joinGame(hostPeerId: string): Promise<GameSession | null> {
        console.log(`[GameIntegration] Joining game: ${hostPeerId}`);
        
        // Se connecter
        const connected = await p2pManager.connect();
        if (!connected) {
            throw new Error('Failed to connect to signaling server');
        }
        
        // Rejoindre la room
        const joined = await p2pManager.joinRoom(hostPeerId);
        if (!joined) {
            throw new Error('Failed to join room');
        }
        
        // Initialiser le GameLoop
        const localPlayerId = p2pManager.getLocalPlayerId();
        this.gameLoop.initializeGame(localPlayerId, this.config.playerName);
        
        // Crer l'UnitController avec le state
        const gameState = this.gameLoop.getGameState();
        this.unitController = new UnitController(gameState);
        
        // Session partielle (attente du snapshot de l'hte)
        this.session = {
            sessionId: hostPeerId,
            isHost: false,
            localPlayerId,
            galaxy: null as unknown as GalaxyData, // Sera reu via snapshot
            state: 'lobby'
        };
        
        this.onSessionUpdate?.(this.session);
        
        console.log('[GameIntegration] Joined game, waiting for host...');
        return this.session;
    }
    
    /**
     * Dmarre la partie (host uniquement).
     */
    startGame(): void {
        if (!this.session?.isHost) {
            console.warn('[GameIntegration] Only host can start game');
            return;
        }
        
        this.session.state = 'starting';
        
        // Spawner les units de dpart pour chaque joueur
        this.spawnStartingUnits();
        
        // Dmarrer via P2P (broadcast START_GAME)
        p2pManager.startGame();
    }
    
    /**
     * Spawn les units de dpart pour tous les joueurs.
     */
    private spawnStartingUnits(): void {
        const gameState = this.gameLoop.getGameState();
        
        // Trouver un systme de dpart pour chaque joueur
        const systems = Array.from(this.session!.galaxy.systems.values());
        let systemIndex = 0;
        
        for (const [playerId, player] of gameState.players) {
            const startSystem = systems[systemIndex % systems.length];
            systemIndex++;
            
            // Spawner le mothership
            const mothershipPos: Position3D = {
                x: startSystem.position.x,
                y: startSystem.position.y + 5,
                z: startSystem.position.z
            };
            
            spawnUnitFree(gameState, playerId, 'MOTHERSHIP', startSystem.id, mothershipPos, 'Mothership');
            
            // Spawner quelques fighters
            for (let i = 0; i < 5; i++) {
                const offset = i * 3;
                spawnUnitFree(gameState, playerId, 'FIGHTER', startSystem.id, {
                    x: mothershipPos.x + offset,
                    y: mothershipPos.y,
                    z: mothershipPos.z + 10
                });
            }
            
            // Spawner des harvesters
            for (let i = 0; i < 2; i++) {
                spawnUnitFree(gameState, playerId, 'HARVESTER', startSystem.id, {
                    x: mothershipPos.x - 10,
                    y: mothershipPos.y,
                    z: mothershipPos.z + i * 5
                });
            }
            
            console.log(`[GameIntegration] Spawned starting units for ${player.name} in ${startSystem.name}`);
        }
    }
    
    // ============================================================================
    // Command Handling
    // ============================================================================
    
    /**
     * Envoie une commande locale (sera broadcaste aux autres joueurs).
     */
    sendCommand(command: GameCommand): void {
        // Excuter localement
        const gameState = this.gameLoop.getGameState();
        const result = this.commandProcessor.process(gameState, command);
        
        if (result.success) {
            // Broadcaster aux autres
            p2pManager.sendCommand(command);
        } else {
            console.warn('[GameIntegration] Command failed:', result.message);
        }
    }
    
    /**
     * Gre une commande reue du rseau.
     */
    private handleRemoteCommand(command: GameCommand, senderId: PlayerId): void {
        // Vrifier que la commande vient bien du joueur
        if (command.playerId !== senderId) {
            console.warn('[GameIntegration] Command sender mismatch');
            return;
        }
        
        // Excuter localement
        const gameState = this.gameLoop.getGameState();
        this.commandProcessor.process(gameState, command);
    }
    
    /**
     * Gre un snapshot reu du rseau.
     */
    private handleSnapshot(state: Partial<GameState>, frameNumber: number): void {
        console.log(`[GameIntegration] Received snapshot for frame ${frameNumber}`);
        
        // Appliquer le snapshot
        const gameState = this.gameLoop.getGameState();
        
        // Merger les donnes (simplifi)
        if (state.units) {
            gameState.units = new Map(state.units);
        }
        if (state.players) {
            gameState.players = new Map(state.players);
        }
        if (state.currentFrame) {
            gameState.currentFrame = state.currentFrame;
        }
        
        // Recrer l'UnitController avec le nouvel tat
        this.unitController = new UnitController(gameState);
    }
    
    /**
     * Gre le dmarrage de partie.
     */
    private handleGameStart(): void {
        if (this.session) {
            this.session.state = 'playing';
            this.onSessionUpdate?.(this.session);
        }
        
        // Dmarrer le GameLoop
        this.gameLoop.start();
        
        console.log('[GameIntegration] Game started!');
        eventBus.emit('game:start', {
            playerId: this.session!.localPlayerId,
            playerName: this.config.playerName
        });
    }
    
    /**
     * Gre la fin de partie.
     */
    private handleGameEnd(): void {
        if (this.session) {
            this.session.state = 'ended';
            this.onSessionUpdate?.(this.session);
        }
        
        // Arrter le GameLoop
        this.gameLoop.stop();
        
        console.log('[GameIntegration] Game ended');
    }
    
    // ============================================================================
    // Raccourcis pour les commandes courantes
    // ============================================================================
    
    /**
     * Ordonne aux units slectionnes de se dplacer.
     */
    moveSelectedUnits(unitIds: EntityId[], target: Position3D): void {
        if (unitIds.length === 0) return;
        
        this.sendCommand({
            type: 'MOVE',
            unitIds,
            target,
            queue: false,
            playerId: this.session!.localPlayerId,
            timestamp: Date.now()
        });
    }
    
    /**
     * Ordonne aux units slectionnes d'attaquer.
     */
    attackWithSelectedUnits(unitIds: EntityId[], targetId: EntityId): void {
        if (unitIds.length === 0) return;
        
        this.sendCommand({
            type: 'ATTACK',
            unitIds,
            targetId,
            queue: false,
            playerId: this.session!.localPlayerId,
            timestamp: Date.now()
        });
    }
    
    /**
     * Arrte les units slectionnes.
     */
    stopSelectedUnits(unitIds: EntityId[]): void {
        if (unitIds.length === 0) return;
        
        this.sendCommand({
            type: 'STOP',
            unitIds,
            playerId: this.session!.localPlayerId,
            timestamp: Date.now()
        });
    }
    
    /**
     * Dfinit la formation des units slectionnes.
     */
    setFormation(unitIds: EntityId[], formation: string): void {
        if (unitIds.length === 0) return;
        
        this.sendCommand({
            type: 'SET_FORMATION',
            unitIds,
            formation,
            playerId: this.session!.localPlayerId,
            timestamp: Date.now()
        });
    }
    
    // ============================================================================
    // Getters
    // ============================================================================
    
    getSession(): GameSession | null {
        return this.session;
    }
    
    getGameState(): GameState | null {
        return this.gameLoop.getGameState();
    }
    
    getUnitController(): UnitController | null {
        return this.unitController;
    }
    
    getFormationManager(): FormationManager {
        return this.formationManager;
    }
    
    isHost(): boolean {
        return this.session?.isHost ?? false;
    }
    
    isPlaying(): boolean {
        return this.session?.state === 'playing';
    }
    
    // ============================================================================
    // Cleanup
    // ============================================================================
    
    /**
     * Nettoie toutes les ressources.
     */
    dispose(): void {
        this.gameLoop.stop();
        p2pManager.leaveRoom();
        GameManager.dispose();
        this.session = null;
        
        console.log('[GameIntegration] Disposed');
    }
}

// ============================================================================
// Factory
// ============================================================================

/**
 * Cre une nouvelle instance de GameIntegration.
 */
export function createGameIntegration(config: GameIntegrationConfig): GameIntegration {
    return new GameIntegration(config);
}
