// src/game/units/UnitController.ts
/**
 * Contrleur complet pour la gestion des units RTS.
 * 
 * Responsabilits:
 * - Cration/destruction d'units
 * - Gestion de la slection
 * - Commandes de masse (ordre  plusieurs units)
 * - Groupes et formations
 * - Queries (recherche par position, type, etc.)
 * 
 * KISS : Logique de contrle pure, pas de rendu.
 */

import type {
  GameState,
  ShipClass,
  Unit,
  SHIP_STATS
} from '../../types/GameState';
import type { EntityId, PlayerId, SystemId, Position3D } from '../../types/commands';
import { eventBus } from '../../core/EventBus';
import { orderMove, orderAttack, orderStop, orderPatrol, orderHarvest } from './UnitBehavior';
import type { FormationType } from '../../ai/FormationManager';

/**
 * Paramtres de cration d'unit.
 */
export interface CreateUnitParams {
  shipClass: ShipClass;
  position: Position3D;
  ownerId: PlayerId;
  systemId: SystemId;
  name?: string;
}

/**
 * Contrleur minimaliste pour la gestion des units (cration, suppression, slection).
 * Dcoupl du rendu et du worker. KISS : aucune logique de dplacement/attaque ici.
 *
 * Utilise GameState.units (Map<EntityId, Unit>) comme source de vrit.
 */
export class UnitController {
  private state: GameState;

  constructor(state: GameState) {
    this.state = state;
  }

  /**
   * Cre une unit et l'ajoute  l'tat global.
   * Utilise la factory createUnit de GameState pour garantir la cohrence.
   */
  createUnit(params: {
    shipClass: ShipClass;
    position: Position3D;
    ownerId: PlayerId;
    systemId: SystemId;
  }): Unit {
    // Gnre l'ID et cre l'unit via la factory
    const id = this.state.nextEntityId++;
    
    const unit: Unit = {
      id,
      ownerId: params.ownerId,
      shipClass: params.shipClass,
      
      systemId: params.systemId,
      position: { ...params.position },
      velocity: { x: 0, y: 0, z: 0 },
      rotation: 0,
      destination: null,
      
      health: 100,
      shield: 50,
      targetId: null,
      combatStance: 'DEFENSIVE',
      attackCooldown: 0,
      
      state: 'IDLE',
      formation: 'NONE',
      formationOffset: { x: 0, y: 0, z: 0 },
      groupLeaderId: null,
      
      cargo: {},
      orderQueue: [],
      
      createdAtFrame: this.state.currentFrame,
      updatedAtFrame: this.state.currentFrame
    };
    
    this.state.units.set(id, unit);
    return unit;
  }

  /**
   * Supprime une unit par id.
   * - soft (dfaut) : marque state='DESTROYED' pour cleanup diffr
   * - hard : retire immdiatement de la Map
   */
  destroyUnit(id: EntityId, hard = false): void {
    const unit = this.state.units.get(id);
    if (!unit) return;
    
    if (hard) {
      this.state.units.delete(id);
    } else {
      unit.state = 'DESTROYED';
      unit.updatedAtFrame = this.state.currentFrame;
    }
  }

  /** Slectionne/dslectionne une unit en modifiant selectedUnitIds. */
  setSelected(id: EntityId, selected: boolean): void {
    const idx = this.state.selectedUnitIds.indexOf(id);
    
    if (selected && idx === -1) {
      this.state.selectedUnitIds.push(id);
    } else if (!selected && idx !== -1) {
      this.state.selectedUnitIds.splice(idx, 1);
    }
  }

  /** Dslectionne toutes les units. */
  clearSelection(): void {
    this.state.selectedUnitIds = [];
  }

  /** Retourne les units slectionnes (filtre les dtruites). */
  getSelectedUnits(): Unit[] {
    return this.state.selectedUnitIds
      .map(id => this.state.units.get(id))
      .filter((u): u is Unit => u !== undefined && u.state !== 'DESTROYED');
  }

  /** Retourne toutes les units actives (non dtruites). */
  getActiveUnits(): Unit[] {
    const active: Unit[] = [];
    for (const unit of this.state.units.values()) {
      if (unit.state !== 'DESTROYED') {
        active.push(unit);
      }
    }
    return active;
  }
  
  /** Retourne une unit par ID, ou undefined si non trouve. */
  getUnit(id: EntityId): Unit | undefined {
    return this.state.units.get(id);
  }
  
  /** Retourne les units d'un joueur donn. */
  getPlayerUnits(playerId: PlayerId): Unit[] {
    const units: Unit[] = [];
    for (const unit of this.state.units.values()) {
      if (unit.ownerId === playerId && unit.state !== 'DESTROYED') {
        units.push(unit);
      }
    }
    return units;
  }
  
  // ============================================================================
  // Commandes de masse
  // ============================================================================
  
  /**
   * Ordonne  plusieurs units de se dplacer vers une position.
   * Calcule automatiquement les offsets pour viter le chevauchement.
   */
  moveUnits(unitIds: EntityId[], target: Position3D, spread = 2): void {
    const units = unitIds.map(id => this.state.units.get(id)).filter((u): u is Unit => !!u);
    if (units.length === 0) return;
    
    // Calculer les positions avec offset
    const count = units.length;
    const cols = Math.ceil(Math.sqrt(count));
    
    units.forEach((unit, i) => {
      const row = Math.floor(i / cols);
      const col = i % cols;
      const offsetX = (col - cols / 2) * spread;
      const offsetZ = (row - Math.ceil(count / cols) / 2) * spread;
      
      orderMove(unit, {
        x: target.x + offsetX,
        y: target.y,
        z: target.z + offsetZ
      });
    });
  }
  
  /**
   * Ordonne  plusieurs units d'attaquer une cible.
   */
  attackWithUnits(unitIds: EntityId[], targetId: EntityId): void {
    for (const id of unitIds) {
      const unit = this.state.units.get(id);
      if (unit && unit.state !== 'DESTROYED') {
        orderAttack(unit, targetId);
      }
    }
  }
  
  /**
   * Ordonne  plusieurs units de s'arrter.
   */
  stopUnits(unitIds: EntityId[]): void {
    for (const id of unitIds) {
      const unit = this.state.units.get(id);
      if (unit) {
        orderStop(unit);
      }
    }
  }
  
  /**
   * Ordonne  plusieurs units de patrouiller.
   */
  patrolUnits(unitIds: EntityId[], waypoints: Position3D[]): void {
    for (const id of unitIds) {
      const unit = this.state.units.get(id);
      if (unit && unit.state !== 'DESTROYED') {
        orderPatrol(unit, waypoints);
      }
    }
  }
  
  /**
   * Dfinit la formation pour un groupe d'units.
   */
  setFormation(unitIds: EntityId[], formation: FormationType): void {
    if (unitIds.length === 0) return;
    
    const leaderId = unitIds[0];
    
    for (const id of unitIds) {
      const unit = this.state.units.get(id);
      if (unit) {
        unit.formation = formation;
        unit.groupLeaderId = id === leaderId ? null : leaderId;
      }
    }
  }
  
  // ============================================================================
  // Queries avances
  // ============================================================================
  
  /**
   * Retourne les units dans un rayon autour d'une position.
   */
  getUnitsInRadius(center: Position3D, radius: number, playerId?: PlayerId): Unit[] {
    const units: Unit[] = [];
    const radiusSq = radius * radius;
    
    for (const unit of this.state.units.values()) {
      if (unit.state === 'DESTROYED') continue;
      if (playerId && unit.ownerId !== playerId) continue;
      
      const dx = unit.position.x - center.x;
      const dy = unit.position.y - center.y;
      const dz = unit.position.z - center.z;
      const distSq = dx * dx + dy * dy + dz * dz;
      
      if (distSq <= radiusSq) {
        units.push(unit);
      }
    }
    
    return units;
  }
  
  /**
   * Retourne les units d'un type donn.
   */
  getUnitsByType(shipClass: ShipClass, playerId?: PlayerId): Unit[] {
    const units: Unit[] = [];
    
    for (const unit of this.state.units.values()) {
      if (unit.state === 'DESTROYED') continue;
      if (unit.shipClass !== shipClass) continue;
      if (playerId && unit.ownerId !== playerId) continue;
      
      units.push(unit);
    }
    
    return units;
  }
  
  /**
   * Retourne les units dans un systme donn.
   */
  getUnitsInSystem(systemId: SystemId, playerId?: PlayerId): Unit[] {
    const units: Unit[] = [];
    
    for (const unit of this.state.units.values()) {
      if (unit.state === 'DESTROYED') continue;
      if (unit.systemId !== systemId) continue;
      if (playerId && unit.ownerId !== playerId) continue;
      
      units.push(unit);
    }
    
    return units;
  }
  
  /**
   * Retourne les ennemis visibles pour une unit.
   */
  getVisibleEnemies(unit: Unit): Unit[] {
    const enemies: Unit[] = [];
    // Note: SHIP_STATS serait import du GameState
    const sensorRange = 100; // Valeur par dfaut
    
    for (const other of this.state.units.values()) {
      if (other.id === unit.id) continue;
      if (other.ownerId === unit.ownerId) continue;
      if (other.state === 'DESTROYED') continue;
      if (other.systemId !== unit.systemId) continue;
      
      const dx = other.position.x - unit.position.x;
      const dy = other.position.y - unit.position.y;
      const dz = other.position.z - unit.position.z;
      const dist = Math.sqrt(dx * dx + dy * dy + dz * dz);
      
      if (dist <= sensorRange) {
        enemies.push(other);
      }
    }
    
    return enemies;
  }
  
  // ============================================================================
  // Statistiques
  // ============================================================================
  
  /**
   * Retourne le nombre d'units par type pour un joueur.
   */
  getUnitCounts(playerId: PlayerId): Map<ShipClass, number> {
    const counts = new Map<ShipClass, number>();
    
    for (const unit of this.state.units.values()) {
      if (unit.ownerId !== playerId) continue;
      if (unit.state === 'DESTROYED') continue;
      
      const current = counts.get(unit.shipClass) ?? 0;
      counts.set(unit.shipClass, current + 1);
    }
    
    return counts;
  }
  
  /**
   * Retourne la valeur totale de la flotte d'un joueur.
   */
  getFleetValue(playerId: PlayerId): number {
    let total = 0;
    
    for (const unit of this.state.units.values()) {
      if (unit.ownerId !== playerId) continue;
      if (unit.state === 'DESTROYED') continue;
      
      // Valeur base sur les stats ( implmenter avec SHIP_STATS)
      total += 100; // Placeholder
    }
    
    return total;
  }
  
  // ============================================================================
  // Nettoyage
  // ============================================================================
  
  /**
   * Supprime les units dtruites de la Map.
   * Appel priodiquement pour librer la mmoire.
   */
  cleanupDestroyedUnits(): number {
    let cleaned = 0;
    
    for (const [id, unit] of this.state.units.entries()) {
      if (unit.state === 'DESTROYED') {
        this.state.units.delete(id);
        cleaned++;
      }
    }
    
    return cleaned;
  }
}