import './styles.css';
import { SceneManager } from './core/SceneManager';
import { GameBootstrap } from './game/GameBootstrap';
import { hud } from './ui/HUD';

/**
 * Point d'entre de l'application PEEJS (MMO RTS spatial).
 *
 * Architecture simplifie :
 * - SceneManager est le point central qui orchestre Three.js et la navigation
 * - GameBootstrap initialise tous les systmes de jeu (HUD, EventBus, rseau)
 * - HUD fournit l'interface utilisateur
 *
 * Flux d'initialisation:
 * 1. SceneManager: Three.js, camra, entits visuelles
 * 2. GameBootstrap: HUD, EventBus, galaxie procdurale, rseau P2P
 * 3. Boucle d'animation dmarre
 */
async function main(): Promise<void> {
    console.log(` PEEJS - Application dmarre - ${new Date().toLocaleTimeString()}`);
    
    try {
        // 1. Initialiser le rendu et la navigation
        const sceneManager = new SceneManager();
        
        // 2. Initialiser les systmes de jeu
        const gameReady = await GameBootstrap.initialize({
            playerName: 'Commander',
            clustersX: 3,
            clustersZ: 3,
            clusterSize: 5,
            multiplayerEnabled: false // Activer pour le multijoueur
        });
        
        if (!gameReady) {
            console.error(' PEEJS - Initialisation du jeu choue');
            return;
        }
        
        // 3. Afficher le HUD
        hud.setVisible(true);
        
        // 4. Lancer la boucle d'animation
        sceneManager.animate();
        
        console.log(' PEEJS - Initialisation termine');
        
        // Exposer pour debug en dveloppement
        if (import.meta.env.DEV) {
            (window as any).sceneManager = sceneManager;
            (window as any).gameBootstrap = GameBootstrap;
            (window as any).hud = hud;
        }
        
    } catch (error) {
        console.error(' PEEJS - Erreur lors de l\'initialisation:', error);
    }
}

// Lancement de l'application
main();
