// src/types/GameState.ts
/**
 * tat global du jeu - Source de vrit centralise.
 * 
 * Architecture:
 * - tat immutable conceptuellement (mutations via actions/commandes)
 * - Srialisable pour snapshots rseau et sauvegarde
 * - Dcoupl du rendu Three.js (pas de rfrences Mesh)
 * 
 * Structure:
 * - Resources: conomie du joueur
 * - Units: Toutes les units du jeu
 * - Structures: Btiments et stations
 * - Players: Donnes des joueurs
 * - Time: Synchronisation temporelle
 */

import type { EntityId, PlayerId, SystemId, Position3D, FormationType } from './commands';

// ============================================================================
// Ressources et conomie
// ============================================================================

/**
 * Types de ressources disponibles dans le jeu.
 */
export type ResourceType = 'credits' | 'metal' | 'crystal' | 'fuel' | 'population';

/**
 * Stock de ressources d'un joueur.
 */
export interface Resources {
  /** Monnaie universelle */
  credits: number;
  
  /** Mtal (rcolt sur astrodes) */
  metal: number;
  
  /** Cristaux (ressource rare) */
  crystal: number;
  
  /** Carburant (pour dplacements longue distance) */
  fuel: number;
  
  /** Population (pour colonies plantaires) */
  population: number;
}

/**
 * Cre un stock de ressources vide.
 */
export function createEmptyResources(): Resources {
  return {
    credits: 0,
    metal: 0,
    crystal: 0,
    fuel: 0,
    population: 0
  };
}

/**
 * Ressources de dpart pour un nouveau joueur.
 */
export function createStartingResources(): Resources {
  return {
    credits: 1000,
    metal: 500,
    crystal: 100,
    fuel: 200,
    population: 0
  };
}

// ============================================================================
// Types d'units et structures
// ============================================================================

/**
 * Catgorie de vaisseau (dtermine taille et rle).
 */
export type ShipClass = 
  | 'FIGHTER'      // Chasseur lger, rapide, faible HP
  | 'CORVETTE'     // Corvette, polyvalente
  | 'FRIGATE'      // Frgate, quilibre
  | 'DESTROYER'    // Destroyer, anti-capital
  | 'CRUISER'      // Croiseur, puissant
  | 'BATTLESHIP'   // Cuirass, trs puissant
  | 'CARRIER'      // Porte-vaisseaux (transporte fighters)
  | 'MOTHERSHIP'   // Vaisseau-mre (base mobile)
  | 'HARVESTER'    // Rcolte ressources
  | 'REPAIR'       // Rparation
  | 'SCOUT'        // Reconnaissance
  // Structures/stations (galement rendues avec ShipRenderer)
  | 'RESEARCH'     // Station de recherche
  | 'STATION'      // Station spatiale
  | 'SHIPYARD'     // Chantier naval
  | 'DEFENSE_PLATFORM'; // Plateforme de dfense

/**
 * Type de structure constructible.
 */
export type StructureType =
  | 'SPACE_STATION'    // Station spatiale (base systme)
  | 'SHIPYARD'         // Chantier naval
  | 'RESEARCH_LAB'     // Laboratoire de recherche
  | 'DEFENSE_PLATFORM' // Plateforme de dfense
  | 'REFINERY'         // Raffinerie (conversion ressources)
  | 'TRADE_HUB'        // Centre commercial
  | 'SENSOR_ARRAY'     // Rseau de capteurs
  | 'JUMP_GATE';       // Porte de saut (voyage rapide)

// ============================================================================
// tats et comportements des units
// ============================================================================

/**
 * tat comportemental d'une unit.
 */
export type UnitState =
  | 'IDLE'           // En attente
  | 'MOVING'         // En dplacement
  | 'ATTACKING'      // En combat
  | 'HARVESTING'     // Rcolte en cours
  | 'DOCKING'        // Amarrage en cours
  | 'DOCKED'         // Amarr
  | 'REPAIRING'      // Rparation en cours
  | 'BUILDING'       // Construction en cours
  | 'PATROLLING'     // Patrouille
  | 'DEFENDING'      // Dfense d'une cible
  | 'RETREATING'     // Repli
  | 'WARPING'        // Voyage inter-systme
  | 'DESTROYED';     // Dtruit (en attente de cleanup)

/**
 * Stance de combat d'une unit.
 */
export type CombatStance =
  | 'AGGRESSIVE'   // Attaque tout ennemi  porte
  | 'DEFENSIVE'    // Attaque si attaqu
  | 'PASSIVE'      // N'attaque jamais automatiquement
  | 'HOLD_FIRE';   // Ne tire pas (ordre explicite)

// ============================================================================
// Donnes d'une unit
// ============================================================================

/**
 * Statistiques de base d'un type d'unit.
 */
export interface UnitStats {
  /** Points de vie maximum */
  maxHealth: number;
  
  /** Points de bouclier maximum */
  maxShield: number;
  
  /** Rgnration bouclier par seconde */
  shieldRegen: number;
  
  /** Points d'armure (rduction dgts) */
  armor: number;
  
  /** Vitesse de dplacement (units/s) */
  speed: number;
  
  /** Vitesse de rotation (rad/s) */
  turnRate: number;
  
  /** Porte de dtection */
  sensorRange: number;
  
  /** Porte d'attaque */
  attackRange: number;
  
  /** Dgts par attaque */
  attackDamage: number;
  
  /** Attaques par seconde */
  attackSpeed: number;
  
  /** Capacit de cargo (pour harvesters) */
  cargoCapacity: number;
  
  /** Cot en ressources */
  cost: Partial<Resources>;
  
  /** Temps de construction (secondes) */
  buildTime: number;
  
  /** Consommation de fuel par unit de distance (inter-systme) */
  fuelConsumption: number;
}

/**
 * Donnes compltes d'une unit (vaisseau).
 */
export interface Unit {
  /** Identifiant unique */
  id: EntityId;
  
  /** Joueur propritaire */
  ownerId: PlayerId;
  
  /** Classe de vaisseau */
  shipClass: ShipClass;
  
  /** Nom personnalis (optionnel) */
  name?: string;
  
  // --- Position et mouvement ---
  
  /** Systme solaire actuel */
  systemId: SystemId;
  
  /** Position dans le systme */
  position: Position3D;
  
  /** Vlocit actuelle */
  velocity: Position3D;
  
  /** Rotation (quaternion simplifi: euler Y uniquement pour 2.5D) */
  rotation: number;
  
  /** Destination courante (null si immobile) */
  destination: Position3D | null;
  
  // --- Combat et sant ---
  
  /** Points de vie actuels */
  health: number;
  
  /** Points de bouclier actuels */
  shield: number;
  
  /** Cible d'attaque courante */
  targetId: EntityId | null;
  
  /** Stance de combat */
  combatStance: CombatStance;
  
  /** Cooldown d'attaque restant (secondes) */
  attackCooldown: number;
  
  // --- tat et comportement ---
  
  /** tat comportemental actuel */
  state: UnitState;
  
  /** Formation courante */
  formation: FormationType;
  
  /** Position dans la formation (offset) */
  formationOffset: Position3D;
  
  /** Chef de groupe (pour formations) */
  groupLeaderId: EntityId | null;
  
  // --- Cargo (harvesters) ---
  
  /** Ressources transportes */
  cargo: Partial<Resources>;
  
  // --- File d'ordres (waypoints, ordres en queue) ---
  
  /** Ordres en attente */
  orderQueue: UnitOrder[];
  
  // --- Timestamps ---
  
  /** Frame de cration */
  createdAtFrame: number;
  
  /** Frame de dernire mise  jour */
  updatedAtFrame: number;
}

/**
 * Ordre en file d'attente pour une unit.
 */
export interface UnitOrder {
  /** Type d'ordre (MOVE, ATTACK, etc.) */
  type: string;
  
  /** Donnes spcifiques  l'ordre */
  data: unknown;
}

// ============================================================================
// Donnes d'une structure
// ============================================================================

/**
 * Donnes compltes d'une structure (station, chantier, etc.).
 */
export interface Structure {
  /** Identifiant unique */
  id: EntityId;
  
  /** Joueur propritaire */
  ownerId: PlayerId;
  
  /** Type de structure */
  structureType: StructureType;
  
  /** Nom personnalis */
  name?: string;
  
  // --- Position ---
  
  /** Systme solaire */
  systemId: SystemId;
  
  /** Position dans le systme */
  position: Position3D;
  
  /** Rotation (Y axis) */
  rotation: number;
  
  // --- Sant ---
  
  /** Points de vie actuels */
  health: number;
  
  /** Points de vie maximum */
  maxHealth: number;
  
  /** Points de bouclier actuels */
  shield: number;
  
  /** Points de bouclier maximum */
  maxShield: number;
  
  // --- Production (si applicable) ---
  
  /** File de production */
  productionQueue: ProductionOrder[];
  
  /** Point de ralliement pour units produites */
  rallyPoint: Position3D | null;
  
  // --- tat ---
  
  /** Construction en cours (0-1) */
  constructionProgress: number;
  
  /** Structure oprationnelle */
  isOperational: boolean;
  
  /** nergie disponible (pour structures) */
  energy: number;
  
  /** nergie maximum */
  maxEnergy: number;
  
  // --- Timestamps ---
  
  createdAtFrame: number;
  updatedAtFrame: number;
}

/**
 * Ordre de production dans une file.
 */
export interface ProductionOrder {
  /** ID unique de l'ordre */
  id: string;
  
  /** Type d'unit ou structure  produire */
  itemType: ShipClass | StructureType;
  
  /** Catgorie (unit ou structure) */
  category: 'unit' | 'structure';
  
  /** Nombre  produire */
  count: number;
  
  /** Nombre dj produit */
  produced: number;
  
  /** Progression de l'item courant (0-1) */
  progress: number;
  
  /** Ressources dj consommes */
  resourcesConsumed: Partial<Resources>;
}

// ============================================================================
// Joueurs
// ============================================================================

/**
 * Relation diplomatique entre joueurs.
 */
export type DiplomaticStatus = 
  | 'NEUTRAL'   // Neutre (pas d'hostilit auto)
  | 'ALLY'      // Alli (ne peut pas attaquer)
  | 'ENEMY'     // Ennemi (attaque automatique)
  | 'WAR';      // En guerre (objectif: limination)

/**
 * Donnes d'un joueur.
 */
export interface Player {
  /** Identifiant unique */
  id: PlayerId;
  
  /** Nom d'affichage */
  displayName: string;
  
  /** Couleur du joueur (hex) */
  color: number;
  
  /** Ressources */
  resources: Resources;
  
  /** Recherches dbloques */
  unlockedTechnologies: string[];
  
  /** Recherche en cours */
  currentResearch: ResearchProgress | null;
  
  /** Relations diplomatiques avec autres joueurs */
  diplomacy: Map<PlayerId, DiplomaticStatus>;
  
  /** Systme de dpart */
  homeSystemId: SystemId;
  
  /** Systmes contrls (avec structures) */
  controlledSystems: SystemId[];
  
  /** Score actuel */
  score: number;
  
  /** Joueur actif (connect) */
  isConnected: boolean;
  
  /** Joueur IA */
  isAI: boolean;
  
  /** Frame de dernire activit */
  lastActiveFrame: number;
}

/**
 * Progression d'une recherche.
 */
export interface ResearchProgress {
  /** ID de la technologie */
  technologyId: string;
  
  /** Progression (0-1) */
  progress: number;
  
  /** Temps restant estim (secondes) */
  estimatedTimeRemaining: number;
}

// ============================================================================
// Technologie
// ============================================================================

/**
 * Dfinition d'une technologie recherchable.
 */
export interface Technology {
  /** Identifiant unique */
  id: string;
  
  /** Nom affich */
  name: string;
  
  /** Description */
  description: string;
  
  /** Cot en ressources */
  cost: Partial<Resources>;
  
  /** Temps de recherche (secondes) */
  researchTime: number;
  
  /** Technologies prrequises */
  prerequisites: string[];
  
  /** Effets (bonus, dblocages) */
  effects: TechnologyEffect[];
}

/**
 * Effet d'une technologie.
 */
export interface TechnologyEffect {
  /** Type d'effet */
  type: 'UNLOCK_UNIT' | 'UNLOCK_STRUCTURE' | 'STAT_BONUS' | 'RESOURCE_BONUS';
  
  /** Cible de l'effet (type d'unit, ressource, etc.) */
  target: string;
  
  /** Valeur du bonus (multiplicateur ou valeur absolue) */
  value: number;
}

// ============================================================================
// Objets clestes et ressources rcoltables
// ============================================================================

/**
 * Type de corps cleste rcoltable.
 */
export type CelestialResourceType =
  | 'ASTEROID_METAL'
  | 'ASTEROID_CRYSTAL'
  | 'GAS_CLOUD'
  | 'DEBRIS_FIELD';

/**
 * Source de ressources rcoltables.
 */
export interface ResourceSource {
  /** Identifiant unique */
  id: EntityId;
  
  /** Systme solaire */
  systemId: SystemId;
  
  /** Position */
  position: Position3D;
  
  /** Type de ressource */
  resourceType: CelestialResourceType;
  
  /** Ressource associe */
  yields: ResourceType;
  
  /** Quantit restante */
  remaining: number;
  
  /** Quantit maximum (pour rgnration) */
  maxAmount: number;
  
  /** Taux de rgnration (units/seconde, 0 = non renouvelable) */
  regenRate: number;
  
  /** Nombre de rcolteurs simultans max */
  maxHarvesters: number;
  
  /** Rcolteurs actuellement assigns */
  assignedHarvesters: EntityId[];
}

// ============================================================================
// tat de synchronisation (rseau)
// ============================================================================

/**
 * tat de synchronisation pour le lockstep.
 */
export interface SyncState {
  /** Frame de simulation actuelle */
  currentFrame: number;
  
  /** Hash de l'tat (pour vrification de dsync) */
  stateHash: string;
  
  /** Frames confirmes par tous les joueurs */
  confirmedFrame: number;
  
  /** Commandes en attente par frame */
  pendingCommands: Map<number, unknown[]>;
  
  /** Joueurs synchroniss */
  syncedPlayers: PlayerId[];
}

// ============================================================================
// tat global du jeu
// ============================================================================

/**
 * tat complet du jeu.
 * Srialisable pour sauvegarde et rseau.
 */
export interface GameState {
  // --- Mta ---
  
  /** Version du format d'tat (pour migrations) */
  version: number;
  
  /** ID de la partie */
  gameId: string;
  
  /** Seed pour gnration procdurale */
  seed: number;
  
  /** Timestamp de cration */
  createdAt: number;
  
  // --- Temps de simulation ---
  
  /** Frame actuelle */
  currentFrame: number;
  
  /** Temps coul en jeu (secondes) */
  gameTime: number;
  
  /** Vitesse de simulation (1.0 = normal) */
  simulationSpeed: number;
  
  /** Pause */
  isPaused: boolean;
  
  /** Timestamp du dernier tick de simulation */
  lastTick: number;
  
  /** Compteur de ticks de simulation */
  tickCount: number;
  
  // --- Entits ---
  
  /** Toutes les units par ID */
  units: Map<EntityId, Unit>;
  
  /** Toutes les structures par ID */
  structures: Map<EntityId, Structure>;
  
  /** Sources de ressources */
  resourceSources: Map<EntityId, ResourceSource>;
  
  // --- Joueurs ---
  
  /** Tous les joueurs */
  players: Map<PlayerId, Player>;
  
  /** ID du joueur local (client-side) */
  localPlayerId: PlayerId | null;
  
  // --- Slection ---
  
  /** IDs des units actuellement slectionnes */
  selectedUnitIds: EntityId[];
  
  // --- Technologie ---
  
  /** Arbre technologique (dfinitions) */
  technologies: Map<string, Technology>;
  
  // --- Rseau ---
  
  /** tat de synchronisation */
  sync: SyncState;
  
  // --- Compteurs (pour gnration d'IDs) ---
  
  /** Prochain ID d'entit */
  nextEntityId: EntityId;
}

// ============================================================================
// Factory functions
// ============================================================================

/**
 * Cre un tat de jeu initial vide.
 */
export function createInitialGameState(gameId: string, seed: number): GameState {
  return {
    version: 1,
    gameId,
    seed,
    createdAt: Date.now(),
    
    currentFrame: 0,
    gameTime: 0,
    simulationSpeed: 1.0,
    isPaused: false,
    lastTick: Date.now(),
    tickCount: 0,
    
    units: new Map(),
    structures: new Map(),
    resourceSources: new Map(),
    
    players: new Map(),
    localPlayerId: null,
    selectedUnitIds: [],
    
    technologies: new Map(),
    
    sync: {
      currentFrame: 0,
      stateHash: '',
      confirmedFrame: 0,
      pendingCommands: new Map(),
      syncedPlayers: []
    },
    
    nextEntityId: 1
  };
}

/**
 * Gnre le prochain ID d'entit.
 */
export function generateEntityId(state: GameState): EntityId {
  const id = state.nextEntityId;
  state.nextEntityId++;
  return id;
}

/**
 * Cre une nouvelle unit avec valeurs par dfaut.
 */
export function createUnit(
  state: GameState,
  ownerId: PlayerId,
  shipClass: ShipClass,
  systemId: SystemId,
  position: Position3D
): Unit {
  const id = generateEntityId(state);
  
  return {
    id,
    ownerId,
    shipClass,
    
    systemId,
    position: { ...position },
    velocity: { x: 0, y: 0, z: 0 },
    rotation: 0,
    destination: null,
    
    health: 100,  //  remplacer par stats du shipClass
    shield: 50,
    targetId: null,
    combatStance: 'DEFENSIVE',
    attackCooldown: 0,
    
    state: 'IDLE',
    formation: 'NONE',
    formationOffset: { x: 0, y: 0, z: 0 },
    groupLeaderId: null,
    
    cargo: {},
    orderQueue: [],
    
    createdAtFrame: state.currentFrame,
    updatedAtFrame: state.currentFrame
  };
}

/**
 * Cre une nouvelle structure avec valeurs par dfaut.
 */
export function createStructure(
  state: GameState,
  ownerId: PlayerId,
  structureType: StructureType,
  systemId: SystemId,
  position: Position3D
): Structure {
  const id = generateEntityId(state);
  
  return {
    id,
    ownerId,
    structureType,
    
    systemId,
    position: { ...position },
    rotation: 0,
    
    health: 1000,   //  remplacer par stats du type
    maxHealth: 1000,
    shield: 500,
    maxShield: 500,
    
    productionQueue: [],
    rallyPoint: null,
    
    constructionProgress: 1.0,  // Complte par dfaut
    isOperational: true,
    energy: 100,
    maxEnergy: 100,
    
    createdAtFrame: state.currentFrame,
    updatedAtFrame: state.currentFrame
  };
}

/**
 * Cre un nouveau joueur.
 */
export function createPlayer(
  id: PlayerId,
  displayName: string,
  color: number,
  homeSystemId: SystemId,
  isAI = false
): Player {
  return {
    id,
    displayName,
    color,
    resources: createStartingResources(),
    unlockedTechnologies: [],
    currentResearch: null,
    diplomacy: new Map(),
    homeSystemId,
    controlledSystems: [homeSystemId],
    score: 0,
    isConnected: true,
    isAI,
    lastActiveFrame: 0
  };
}

