export type PlayerState = Player;

// ============================================================================
// Factory functions additionnelles
// ============================================================================

/**
 * Cre un tat de jeu par dfaut (wrapper de createInitialGameState).
 * Utilise un gameId et seed gnrs automatiquement.
 */
export function createGameState(): GameState {
  const gameId = `game-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  const seed = Date.now();
  return createInitialGameState(gameId, seed);
}

/**
 * Alias de createGameState pour compatibilit.
 */
export const createDefaultGameState = createGameState;

