import * as THREE from 'three';
import { SunMetadata } from '../entities/ClusterGrid';

/**
 * tats de navigation possibles dans l'application
 */
export type ViewMode = 'GALAXY' | 'SYSTEM' | 'PLANET';

/**
 * Informations compltes sur un systme solaire slectionn
 * Combine mtadonnes ET rfrences Three.js
 */
export interface SystemReference {
  /**
   * Mtadonnes du systme (source de vrit stable).
   * Important: elles restent valides mme si le rfrentiel Three.js change (scene graph swap).
   */
  metadata: SunMetadata;

  /**
   * Rfrences runtime Three.js (optionnelles).
   * En vue GALAXY, elles existent gnralement (raycast / rendu).
   * Ne pas les utiliser comme source de vrit lors d'un changement de rfrentiel.
   */
  sunMesh?: THREE.Mesh | null;
  pickMesh?: THREE.Mesh | null;
}

/**
 * Rfrence vers une plante dans un systme solaire
 * Architecture: Gomtrie sphrique stricte (AUCUNE projection plane)
 */
export interface PlanetReference {
  /** ID unique de la plante */
  id: string;
  
  /** Nom affich */
  name: string;
  
  /** Rayon de la plante (units Three.js) */
  radius: number;
  
  /** Position relative au soleil (coordonnes locales systme) */
  position: { x: number; y: number; z: number };
  
  /** Seed pour gnration procdurale reproductible */
  seed: number;
  
  /** Type de plante (dtermine apparence de base) */
  type: 'telluric' | 'gas' | 'ice' | 'desert';
}

/**
 * tat centralis de la navigation
 * Source de vrit unique pour tous les composants
 */
export interface NavigationState {
  /** Vue actuellement active */
  currentView: ViewMode;
  
  /** Systme solaire actuellement slectionn/visit (null si vue galaxie sans slection) */
  currentSystem: SystemReference | null;
  
  /** Plante actuellement visite en vue SURFACE (null si pas en vue plante) */
  currentPlanet: PlanetReference | null;
  
  /**
   * Pile d'historique navigation pour retours (back)
   * Permet de revenir au systme prcdent lors de navigation multi-niveaux
   */
  navigationHistory: SystemReference[];
  
  /**
   * Distance optimale de l'objet actuellement cibl (soleil ou plante)
   * Utilise pour dclencher les transitions automatiques bases sur le zoom
   * null en vue GALAXY sans slection
   */
  currentOptimalDistance: number | null;
}

/**
 * Crer un tat navigation initial (vue galaxie, aucune slection)
 */
export function createInitialNavigationState(): NavigationState {
  return {
    currentView: 'GALAXY',
    currentSystem: null,
    currentPlanet: null,
    navigationHistory: [],
    currentOptimalDistance: null
  };
}