import * as THREE from 'three';
import type { NavigationState, ViewMode, PlanetReference } from '../types/NavigationState';
import type { SunMetadata } from '../entities/ClusterGrid';
import { SolarSystem } from '../entities/SolarSystem';
import { PlanetSurface } from '../entities/PlanetSurface';
import type { IUpdatable } from './IUpdatable';

/**
 * Gre la visibilit et le cycle de vie des entits 3D selon la vue de navigation.
 * 
 * Responsabilits :
 * - Afficher/masquer les entits selon ViewMode (GALAXY, SYSTEM, PLANET)
 * - Lazy loading de SolarSystem et PlanetSurface (cration  la demande)
 * - Propagation des updates aux entits actives
 * - Nettoyage des ressources lors des transitions
 * 
 * Architecture :
 * - GALAXY : galaxyGroup visible, SolarSystem masqu, PlanetSurface masque
 * - SYSTEM : SolarSystem visible (cr si ncessaire), galaxyGroup masqu optionnellement
 * - PLANET : PlanetSurface visible (cre si ncessaire), SolarSystem en arrire-plan
 * 
 * Principe KISS : logique simple de visibilit base sur l'tat NavigationState.
 * Pas de sur-ingnierie, pas de machine  tats complexe.
 */
export class ViewEntitiesManager implements IUpdatable {
    /** Rfrence  la scne Three.js pour ajout/retrait d'entits */
    private scene: THREE.Scene;

    /** Groupe contenant les entits GALAXY (ClusterGrid, StarField) - toujours prsent */
    private galaxyGroup: THREE.Group;

    /** SolarSystem actuel, cr  la demande lors de l'entre dans un systme */
    private currentSolarSystem: SolarSystem | null = null;

    /** PlanetSurface actuelle, cre  la demande lors de l'entre sur une plante */
    private currentPlanetSurface: PlanetSurface | null = null;

    /** Vue actuellement active (cache pour viter les mises  jour inutiles) */
    private currentView: ViewMode = 'GALAXY';

    /** Rfrence  la camra pour updateLOD de PlanetSurface */
    private camera: THREE.Camera | null = null;

    /**
     * Cre un nouveau ViewEntitiesManager.
     * 
     * @param scene - Scne Three.js o les entits sont ajoutes/retires
     * @param galaxyGroup - Groupe contenant ClusterGrid et StarField (toujours prsent)
     */
    constructor(scene: THREE.Scene, galaxyGroup: THREE.Group) {
        this.scene = scene;
        this.galaxyGroup = galaxyGroup;

        // S'assurer que le galaxyGroup est visible au dpart (vue GALAXY par dfaut)
        this.galaxyGroup.visible = true;
    }

    /**
     * Met  jour la visibilit des entits selon la vue actuelle.
     * Cre les entits  la demande (lazy loading) et dtruit les anciennes si ncessaire.
     * 
     * Logique de visibilit :
     * - GALAXY : galaxyGroup visible, pas de SolarSystem/PlanetSurface
     * - SYSTEM : galaxyGroup masqu, SolarSystem visible
     * - PLANET : galaxyGroup masqu, SolarSystem visible (arrire-plan), PlanetSurface visible
     * 
     * @param view - Mode de vue cible (GALAXY, SYSTEM, PLANET)
     * @param navState - tat de navigation complet (pour accder aux rfrences systme/plante)
     */
    public setView(view: ViewMode, navState: NavigationState): void {
        // Optimisation : ne rien faire si la vue n'a pas chang
        // Sauf si les rfrences systme/plante ont chang (nouvelle slection)
        const systemChanged = this.hasSystemChanged(navState);
        const planetChanged = this.hasPlanetChanged(navState);

        if (view === this.currentView && !systemChanged && !planetChanged) {
            return;
        }

        this.currentView = view;

        switch (view) {
            case 'GALAXY':
                this.showGalaxyView();
                break;
            case 'SYSTEM':
                this.showSystemView(navState);
                break;
            case 'PLANET':
                this.showPlanetView(navState);
                break;
        }
    }

    /**
     * Vrifie si le systme slectionn a chang.
     */
    private hasSystemChanged(navState: NavigationState): boolean {
        if (!navState.currentSystem) {
            return this.currentSolarSystem !== null;
        }
        if (!this.currentSolarSystem) {
            return true;
        }
        // Comparer par ID de mtadonnes (source de vrit stable)
        return navState.currentSystem.metadata.id !== this.currentSolarSystem.getMetadata().id;
    }

    /**
     * Vrifie si la plante slectionne a chang.
     */
    private hasPlanetChanged(navState: NavigationState): boolean {
        if (!navState.currentPlanet) {
            return this.currentPlanetSurface !== null;
        }
        if (!this.currentPlanetSurface) {
            return true;
        }
        // Comparer par ID de plante
        return navState.currentPlanet.id !== this.currentPlanetSurface.getPlanetReference().id;
    }

    /**
     * Affiche la vue GALAXY : galaxyGroup visible, autres entits masques/dtruites.
     */
    private showGalaxyView(): void {
        // Afficher la galaxie
        this.galaxyGroup.visible = true;

        // Nettoyer PlanetSurface si existante
        this.disposePlanetSurface();

        // Nettoyer SolarSystem si existant
        this.disposeSolarSystem();
    }

    /**
     * Affiche la vue SYSTEM : SolarSystem visible, galaxyGroup masqu.
     * Cre le SolarSystem  la demande si ncessaire.
     */
    private showSystemView(navState: NavigationState): void {
        // Masquer la galaxie
        this.galaxyGroup.visible = false;

        // Nettoyer PlanetSurface si existante (on sort d'une plante vers le systme)
        this.disposePlanetSurface();

        // Crer ou mettre  jour le SolarSystem
        if (navState.currentSystem) {
            this.ensureSolarSystem(navState.currentSystem.metadata);
        } else {
            // Cas anormal : vue SYSTEM sans systme slectionn
            console.warn('[ViewEntitiesManager] Vue SYSTEM sans currentSystem dfini');
            this.disposeSolarSystem();
        }
    }

    /**
     * Affiche la vue PLANET : PlanetSurface visible, SolarSystem en arrire-plan.
     * Cre la PlanetSurface  la demande si ncessaire.
     */
    private showPlanetView(navState: NavigationState): void {
        // Masquer la galaxie
        this.galaxyGroup.visible = false;

        // S'assurer que le SolarSystem est prsent (arrire-plan)
        if (navState.currentSystem) {
            this.ensureSolarSystem(navState.currentSystem.metadata);
        }

        // Crer la PlanetSurface
        if (navState.currentPlanet) {
            this.ensurePlanetSurface(navState.currentPlanet);
        } else {
            // Cas anormal : vue PLANET sans plante slectionne
            console.warn('[ViewEntitiesManager] Vue PLANET sans currentPlanet dfini');
            this.disposePlanetSurface();
        }
    }

    /**
     * S'assure qu'un SolarSystem est cr et ajout  la scne.
     * Si un SolarSystem diffrent existe dj, il est remplac.
     * 
     * @param metadata - Mtadonnes du systme  afficher
     */
    private ensureSolarSystem(metadata: SunMetadata): void {
        // Vrifier si on a dj le bon systme
        if (this.currentSolarSystem && 
            this.currentSolarSystem.getMetadata().id === metadata.id) {
            // Systme dj charg, s'assurer qu'il est visible
            this.currentSolarSystem.getGroup().visible = true;
            return;
        }

        // Nettoyer l'ancien systme si diffrent
        this.disposeSolarSystem();

        // Crer le nouveau SolarSystem (lazy loading)
        console.log(`[ViewEntitiesManager] Cration SolarSystem: ${metadata.name}`);
        this.currentSolarSystem = new SolarSystem(metadata);
        
        // Ajouter  la scne
        this.scene.add(this.currentSolarSystem.getGroup());
    }

    /**
     * S'assure qu'une PlanetSurface est cre et ajoute  la scne.
     * Si une PlanetSurface diffrente existe dj, elle est remplace.
     *
     * @param planetRef - Rfrence de la plante  afficher
     */
    private ensurePlanetSurface(planetRef: PlanetReference): void {
        // Vrifier si on a dj la bonne plante
        if (this.currentPlanetSurface &&
            this.currentPlanetSurface.getPlanetReference().id === planetRef.id) {
            // Plante dj charge, s'assurer qu'elle est visible
            this.currentPlanetSurface.getGroup().visible = true;
            return;
        }

        // Nettoyer l'ancienne plante si diffrente
        this.disposePlanetSurface();

        // Crer la nouvelle PlanetSurface (lazy loading)
        console.log(`[ViewEntitiesManager] Cration PlanetSurface: ${planetRef.name}`);
        this.currentPlanetSurface = new PlanetSurface(planetRef);
        
        // Positionner la plante selon sa position dans le systme
        const group = this.currentPlanetSurface.getGroup();
        group.position.set(planetRef.position.x, planetRef.position.y, planetRef.position.z);
        
        // Ajouter un grand repre 3D (AxesHelper)  l'origine de la plante pour debug/orientation
        // Rouge = X, Vert = Y, Bleu = Z
        // Taille proportionnelle au rayon de la plante (2 rayon pour tre bien visible)
        const axesHelper = new THREE.AxesHelper(planetRef.radius * 2);
        axesHelper.position.set(0, 0, 0); // Origine locale du groupe plante
        group.add(axesHelper);
        console.log(`[ViewEntitiesManager] AxesHelper ajout  l'origine de PlanetSurface (taille: ${(planetRef.radius * 2).toFixed(2)})`);
        
        // Ajouter  la scne
        this.scene.add(group);
    }

    /**
     * Nettoie et supprime le SolarSystem actuel.
     */
    private disposeSolarSystem(): void {
        if (this.currentSolarSystem) {
            console.log(`[ViewEntitiesManager] Dispose SolarSystem: ${this.currentSolarSystem.getMetadata().name}`);
            this.scene.remove(this.currentSolarSystem.getGroup());
            this.currentSolarSystem.dispose();
            this.currentSolarSystem = null;
        }
    }

    /**
     * Nettoie et supprime la PlanetSurface actuelle.
     */
    private disposePlanetSurface(): void {
        if (this.currentPlanetSurface) {
            console.log(`[ViewEntitiesManager] Dispose PlanetSurface: ${this.currentPlanetSurface.getPlanetReference().name}`);
            this.scene.remove(this.currentPlanetSurface.getGroup());
            this.currentPlanetSurface.dispose();
            this.currentPlanetSurface = null;
        }
    }

    /**
     * Retourne le SolarSystem actuel (ou null si non charg).
     * Utile pour le raycasting et la slection de plantes en vue SYSTEM.
     */
    public getSolarSystem(): SolarSystem | null {
        return this.currentSolarSystem;
    }

    /**
     * Retourne la PlanetSurface actuelle (ou null si non charge).
     * Utile pour le raycasting et l'interaction en vue PLANET.
     */
    public getPlanetSurface(): PlanetSurface | null {
        return this.currentPlanetSurface;
    }

    /**
     * Retourne la vue actuellement active.
     */
    public getCurrentView(): ViewMode {
        return this.currentView;
    }

    /**
     * Dfinit la rfrence  la camra pour les mises  jour LOD.
     * Doit tre appel aprs la cration du CameraManager.
     * 
     * @param camera - Camra Three.js pour calcul des distances LOD
     */
    public setCamera(camera: THREE.Camera): void {
        this.camera = camera;
    }

    /**
     * Update appel chaque frame (implmentation IUpdatable).
     * Propage l'update aux entits actives selon la vue courante.
     * 
     * @param deltaTime - Temps coul depuis la dernire frame (secondes)
     * @param cameraPosition - Position de la camra (optionnel, pour compatibilit IUpdatable)
     */
    public update(deltaTime: number, cameraPosition?: THREE.Vector3): void {
        // Update SolarSystem si actif (vues SYSTEM ou PLANET)
        if (this.currentSolarSystem && 
            (this.currentView === 'SYSTEM' || this.currentView === 'PLANET')) {
            this.currentSolarSystem.update(deltaTime);
        }

        // Update PlanetSurface si active (vue PLANET uniquement)
        if (this.currentPlanetSurface && this.currentView === 'PLANET') {
            this.currentPlanetSurface.update(deltaTime);
            
            // Mettre  jour le LOD si on a une camra
            if (this.camera) {
                this.currentPlanetSurface.updateLOD(this.camera);
            }
        }
    }

    /**
     * Libre toutes les ressources (pattern Dispose).
     * Doit tre appel lors de la destruction du manager.
     */
    public dispose(): void {
        this.disposePlanetSurface();
        this.disposeSolarSystem();
        
        // Note : galaxyGroup n'est pas dispos ici car il est gr par SceneManager
        // qui le possde et le passe au constructeur
        
        this.camera = null;
        console.log('[ViewEntitiesManager] Disposed');
    }
}