// src/game/units/UnitFactory.ts
/**
 * Factory pour la cration d'units (vaisseaux).
 * 
 * Responsabilits:
 * - Crer des units avec les stats appropries
 * - Valider les ressources disponibles
 * - Gnrer les IDs uniques
 * - Initialiser l'tat des units
 * 
 * KISS: Pas de patterns complexes, juste des fonctions pures.
 */

import {
  GameState,
  Unit,
  ShipClass,
  SHIP_STATS,
  Resources,
  generateEntityId,
  CombatStance,
  UnitState
} from '../../types/GameState';
import type { EntityId, PlayerId, SystemId, Position3D } from '../../types/commands';

// ============================================================================
// Validation
// ============================================================================

/**
 * Vrifie si un joueur a les ressources ncessaires pour construire une unit.
 * 
 * @param playerResources - Ressources actuelles du joueur
 * @param shipClass - Classe de vaisseau  construire
 * @returns true si les ressources sont suffisantes
 */
export function canAffordUnit(
  playerResources: Resources,
  shipClass: ShipClass
): boolean {
  const cost = SHIP_STATS[shipClass].cost;
  
  for (const [resource, amount] of Object.entries(cost)) {
    if (amount && playerResources[resource as keyof Resources] < amount) {
      return false;
    }
  }
  
  return true;
}

/**
 * Dduit le cot d'une unit des ressources du joueur.
 * Modifie playerResources en place.
 * 
 * @param playerResources - Ressources du joueur (mutes)
 * @param shipClass - Classe de vaisseau
 */
export function deductUnitCost(
  playerResources: Resources,
  shipClass: ShipClass
): void {
  const cost = SHIP_STATS[shipClass].cost;
  
  for (const [resource, amount] of Object.entries(cost)) {
    if (amount) {
      playerResources[resource as keyof Resources] -= amount;
    }
  }
}

// ============================================================================
// Cration d'units
// ============================================================================

/**
 * Cre une nouvelle unit avec les stats de base.
 * N'ajoute PAS l'unit au GameState (responsabilit de l'appelant).
 * 
 * @param state - tat du jeu (pour gnrer l'ID)
 * @param ownerId - ID du joueur propritaire
 * @param shipClass - Classe de vaisseau
 * @param systemId - Systme solaire o spawn l'unit
 * @param position - Position de spawn
 * @param name - Nom personnalis optionnel
 * @returns Nouvelle unit initialise
 */
export function createUnitFromClass(
  state: GameState,
  ownerId: PlayerId,
  shipClass: ShipClass,
  systemId: SystemId,
  position: Position3D,
  name?: string
): Unit {
  const id = generateEntityId(state);
  const stats = SHIP_STATS[shipClass];
  
  const unit: Unit = {
    id,
    ownerId,
    shipClass,
    name,
    
    // Position et mouvement
    systemId,
    position: { ...position },
    velocity: { x: 0, y: 0, z: 0 },
    rotation: 0,
    destination: null,
    
    // Combat et sant (initialiss aux valeurs max)
    health: stats.maxHealth,
    shield: stats.maxShield,
    targetId: null,
    combatStance: 'DEFENSIVE',
    attackCooldown: 0,
    
    // tat et comportement
    state: 'IDLE',
    formation: 'NONE',
    formationOffset: { x: 0, y: 0, z: 0 },
    groupLeaderId: null,
    
    // Cargo (vide par dfaut)
    cargo: {},
    orderQueue: [],
    
    // Timestamps
    createdAtFrame: state.currentFrame,
    updatedAtFrame: state.currentFrame
  };
  
  return unit;
}

/**
 * Spawn une unit complte dans le jeu.
 * Vrifie les ressources, les dduit, cre l'unit et l'ajoute au state.
 * 
 * @param state - tat du jeu (mut)
 * @param ownerId - ID du joueur
 * @param shipClass - Classe de vaisseau
 * @param systemId - Systme de spawn
 * @param position - Position de spawn
 * @returns L'unit cre, ou null si ressources insuffisantes
 */
export function spawnUnit(
  state: GameState,
  ownerId: PlayerId,
  shipClass: ShipClass,
  systemId: SystemId,
  position: Position3D
): Unit | null {
  const player = state.players.get(ownerId);
  if (!player) {
    console.warn(`[UnitFactory] Player not found: ${ownerId}`);
    return null;
  }
  
  // Vrifier les ressources
  if (!canAffordUnit(player.resources, shipClass)) {
    console.warn(`[UnitFactory] Insufficient resources for ${shipClass}`);
    return null;
  }
  
  // Dduire le cot
  deductUnitCost(player.resources, shipClass);
  
  // Crer l'unit
  const unit = createUnitFromClass(
    state,
    ownerId,
    shipClass,
    systemId,
    position
  );
  
  // Ajouter au state
  state.units.set(unit.id, unit);
  
  console.log(`[UnitFactory] Spawned ${shipClass} (ID: ${unit.id}) for player ${ownerId}`);
  
  return unit;
}

/**
 * Spawn une unit gratuite (pour tests ou vnements spciaux).
 * Ne vrifie pas les ressources.
 */
export function spawnUnitFree(
  state: GameState,
  ownerId: PlayerId,
  shipClass: ShipClass,
  systemId: SystemId,
  position: Position3D,
  name?: string
): Unit {
  const unit = createUnitFromClass(state, ownerId, shipClass, systemId, position, name);
  state.units.set(unit.id, unit);
  return unit;
}

// ============================================================================
// Suppression d'units
// ============================================================================

/**
 * Dtruit une unit et la retire du jeu.
 * 
 * @param state - tat du jeu
 * @param unitId - ID de l'unit  dtruire
 * @returns true si l'unit a t supprime
 */
export function destroyUnit(state: GameState, unitId: EntityId): boolean {
  const unit = state.units.get(unitId);
  if (!unit) return false;
  
  // Marquer comme dtruite avant suppression (pour vnements)
  unit.state = 'DESTROYED';
  unit.updatedAtFrame = state.currentFrame;
  
  // Nettoyer les rfrences
  // - Retirer des cibles d'autres units
  for (const otherUnit of state.units.values()) {
    if (otherUnit.targetId === unitId) {
      otherUnit.targetId = null;
    }
    if (otherUnit.groupLeaderId === unitId) {
      otherUnit.groupLeaderId = null;
    }
  }
  
  // Supprimer du state
  state.units.delete(unitId);
  
  console.log(`[UnitFactory] Destroyed unit ${unitId}`);
  
  return true;
}

// ============================================================================
// Requtes
// ============================================================================

/**
 * Rcupre toutes les units d'un joueur.
 */
export function getPlayerUnits(state: GameState, playerId: PlayerId): Unit[] {
  return Array.from(state.units.values()).filter(u => u.ownerId === playerId);
}

/**
 * Rcupre toutes les units dans un systme.
 */
export function getSystemUnits(state: GameState, systemId: SystemId): Unit[] {
  return Array.from(state.units.values()).filter(u => u.systemId === systemId);
}

/**
 * Rcupre les units d'un joueur dans un systme spcifique.
 */
export function getPlayerUnitsInSystem(
  state: GameState,
  playerId: PlayerId,
  systemId: SystemId
): Unit[] {
  return Array.from(state.units.values()).filter(
    u => u.ownerId === playerId && u.systemId === systemId
  );
}

/**
 * Rcupre les units ennemies visibles depuis une position.
 */
export function getVisibleEnemies(
  state: GameState,
  playerId: PlayerId,
  systemId: SystemId,
  position: Position3D,
  sensorRange: number
): Unit[] {
  const player = state.players.get(playerId);
  if (!player) return [];
  
  return Array.from(state.units.values()).filter(unit => {
    // Mme systme
    if (unit.systemId !== systemId) return false;
    
    // Pas le mme joueur
    if (unit.ownerId === playerId) return false;
    
    // Vrifier relation diplomatique (ennemi ou en guerre)
    const relation = player.diplomacy.get(unit.ownerId);
    if (relation !== 'ENEMY' && relation !== 'WAR') return false;
    
    //  porte de dtection
    const dx = unit.position.x - position.x;
    const dy = unit.position.y - position.y;
    const dz = unit.position.z - position.z;
    const distSq = dx * dx + dy * dy + dz * dz;
    
    return distSq <= sensorRange * sensorRange;
  });
}

// ============================================================================
// Utilitaires
// ============================================================================

/**
 * Calcule la distance entre deux positions.
 */
export function distance3D(a: Position3D, b: Position3D): number {
  const dx = b.x - a.x;
  const dy = b.y - a.y;
  const dz = b.z - a.z;
  return Math.sqrt(dx * dx + dy * dy + dz * dz);
}

/**
 * Vrifie si une unit est en combat (attaque ou est attaque).
 */
export function isInCombat(unit: Unit): boolean {
  return unit.state === 'ATTACKING' || unit.targetId !== null;
}

/**
 * Vrifie si une unit peut attaquer.
 */
export function canAttack(unit: Unit): boolean {
  const stats = SHIP_STATS[unit.shipClass];
  return stats.attackDamage > 0 && unit.attackCooldown <= 0;
}

/**
 * Vrifie si une unit est vivante.
 */
export function isAlive(unit: Unit): boolean {
  return unit.health > 0 && unit.state !== 'DESTROYED';
}