/**
 * Configuration gnrale de l'application MMORTS
 */

// Test hot reload - nouvelle constante
export const APP_VERSION = '1.0.0-hot-reload-test';

export const CONFIG = {
    debug: true,
    maxUnits: 1000,
    enableWorkers: true
};

// Configuration de la grille de cubes
export const GRID_CONFIG = {
    /** Nombre de cubes sur l'axe X */
    cubesX: 10,
    /** Nombre de cubes sur l'axe Y (hauteur) */
    cubesY: 1,
    /** Nombre de cubes sur l'axe Z */
    cubesZ: 10,
};

// Configuration du mode debug
export const DEBUG_CONFIG = {
    /** Afficher les pick meshes (cubes de slection invisibles) */
    showPickMeshes: false,
};

// Configuration du style visuel
export const VISUAL_CONFIG = {
    /** Opacit des cubes non-actifs (0.0 = invisible, 1.0 = opaque) */
    inactiveCubeOpacity: 0.3,
    /** Opacit des cubes actifs (hover ou slection) */
    activeCubeOpacity: 1.0,
};

// Configuration de la camra
export const CAMERA_CONFIG = {
    /** Position initiale de la camra (X, Y, Z) */
    initialPosition: {
        x: -12,
        y: 12,
        z: 0,
    },
    /** Point cibl par la camra (X, Y, Z) */
    lookAt: {
        x: 0,
        y: 0,
        z: 0,
    },
    /** Field of view */
    fov: 60,
    /** Distance minimale de zoom */
    minDistance: 0.5,
    /** Distance maximale de zoom */
    maxDistance: 20,
    /** Vitesse de dplacement avec WASD */
    moveSpeed: 0.5,
};

// Configuration de la simulation
export const SIMULATION_CONFIG = {
    /** Nombre maximum d'units dans la simulation */
    maxUnits: 10000,
};

// Configuration de la navigation par zoom
export const NAVIGATION_CONFIG = {
    /** 
     * Distance d'entre dans un systme solaire (facteur de optimalDistance)
     * Ex: 0.5 = on entre quand distance < 50% de optimalDistance
     */
    systemEnterDistanceFactor: 0.5,
    
    /** 
     * Distance de sortie d'un systme solaire (facteur de optimalDistance)
     * Ex: 2.5 = on sort quand distance > 250% de optimalDistance
     */
    systemExitDistanceFactor: 2.5,
    
    /** 
     * Distance d'entre dans une plante (base sur le rayon de la plante)
     * Ex: 3.0 = on entre quand distance < 3 * rayon de la plante
     */
    planetEnterDistanceFactor: 3.0,
    
    /** 
     * Distance de sortie d'une plante (base sur le rayon de la plante)
     * Ex: 8.0 = on sort quand distance > 8 * rayon de la plante
     */
    planetExitDistanceFactor: 8.0,
    
    /**
     * Dlai minimum entre deux transitions automatiques (ms)
     * vite les oscillations rapides entre tats
     */
    transitionCooldown: 500,
};
