import * as THREE from 'three';
import type { PlanetReference } from '../types/NavigationState';
import { NoiseGenerator } from '../utils/NoiseGenerator';

/**
 * Reprsente la surface sphrique d'une plante avec gnration procdurale
 *
 * Architecture Phase 2.3: Textures procdurales + Relief topographique
 * - ShaderMaterial GLSL pour gnration GPU (performances)
 * - Heightmap via FBM (Fractional Brownian Motion, 5 octaves)
 * - Displacement mapping le long des normales (5% radius maximal)
 * - Biomes adaptatifs selon type plante (telluric/desert/ice/gas)
 * - Seed-based reproductible (mme plante = mmes patterns)
 *
 * Architecture Phase 2.2: Systme LOD multi-niveaux
 * - Gomtrie sphrique pure (AUCUNE projection plane autorise)
 * - Adaptation automatique du niveau de dtail selon distance camra
 * - 4 niveaux LOD: 128128 (haute), 6464 (moyenne), 3232 (basse), 1616 (trs basse)
 * - Seuils: 2, 5, 10, 20 radius pour transitions invisibles
 * - Material partag entre niveaux (optimisation mmoire)
 * - Utilisation THREE.LOD natif (gestion automatique)
 *
 * Contrainte architecturale CRITIQUE:
 * La plante DOIT rester sphrique en toutes circonstances.
 * Le displacement est appliqu le long des normales pour prserver la sphricit.
 */
export class PlanetSurface {
    private group: THREE.Group;
    private lodGroup: THREE.LOD;
    private geometries: THREE.SphereGeometry[] = [];
    private materials: THREE.ShaderMaterial[] = [];
    private meshes: THREE.Mesh[] = [];
    private planetRef: PlanetReference;

    /**
     * Cre ShaderMaterial procdural pour surface plantaire
     * Phase 2.3: Gnration textures + relief GPU
     *
     * Vertex shader: Displacement heightmap le long normales (relief topographique)
     * Fragment shader: Couleurs biomes adaptatifs selon type plante
     *
     * Justifications techniques:
     * - GPU: performances (calcul parallle massivement)
     * - Seed: reproductibilit garantie (dterminisme)
     * - FBM 5 octaves: dtails multi-chelle ralistes
     * - Displacement 5% radius: relief visible sans dformation excessive
     *
     * @param planetRef Rfrence plante (type, seed, radius)
     * @returns ShaderMaterial avec vertex/fragment shaders personnaliss
     */
    private createProceduralMaterial(planetRef: PlanetReference): THREE.ShaderMaterial {
        // Normaliser seed pour uniformit (0-1)
        const normalizedSeed = (planetRef.seed % 10000) / 10000.0;

        const vertexShader = `
            ${NoiseGenerator.getGLSLNoiseFunction()}

            uniform float seed;
            uniform float radius;
            uniform float displacementScale;
            
            varying vec3 vPosition;
            varying vec3 vNormal;
            varying float vHeight;
            
            void main() {
                vPosition = position;
                vNormal = normal;
                
                // Gnrer heightmap via FBM (5 octaves, lacunarity 2.0, gain 0.5)
                // Position normalise pour cohrence sphrique
                vec3 noisePos = normalize(position) * seed * 10.0;
                float height = fbm(noisePos, 5, 2.0, 0.5);
                
                // Normaliser height [-1,1] -> [0,1]
                height = (height + 1.0) * 0.5;
                vHeight = height;
                
                // Appliquer displacement le long des normales (CRITRE SPHRICIT)
                // Prserve la forme sphrique en dplaant radialement
                vec3 displacedPosition = position + normal * height * displacementScale;
                
                gl_Position = projectionMatrix * modelViewMatrix * vec4(displacedPosition, 1.0);
            }
        `;

        const fragmentShader = `
            ${NoiseGenerator.getGLSLNoiseFunction()}
            ${NoiseGenerator.getGLSLBiomeColors(planetRef.type)}

            uniform float seed;
            
            varying vec3 vPosition;
            varying vec3 vNormal;
            varying float vHeight;
            
            void main() {
                // Gnrer moisture map (humidit) via noise secondaire
                // Seed diffrent pour dcorrlation height/moisture
                vec3 moisturePos = normalize(vPosition) * seed * 15.0;
                float moisture = fbm(moisturePos, 3, 2.0, 0.5);
                
                // Normaliser moisture [-1,1] -> [0,1]
                moisture = (moisture + 1.0) * 0.5;
                
                // Calculer couleur biome (adapt au type plante)
                vec3 baseColor = getBiomeColor(vHeight, moisture);
                
                // Lighting simple (diffuse Lambert)
                // Direction lumire fixe (soleil au centre systme)
                vec3 lightDir = normalize(vec3(1.0, 1.0, 1.0));
                float diff = max(dot(vNormal, lightDir), 0.0);
                
                // Terme ambient (0.3) + diffuse (0.7)
                vec3 finalColor = baseColor * (0.3 + 0.7 * diff);
                
                gl_FragColor = vec4(finalColor, 1.0);
            }
        `;

        // Calculer displacement scale adapt au rayon (5% rayon = relief maximal)
        // Justification: visible en vue rapproche, pas excessif en vue loigne
        const displacementScale = planetRef.radius * 0.05;

        return new THREE.ShaderMaterial({
            uniforms: {
                seed: { value: normalizedSeed },
                radius: { value: planetRef.radius },
                displacementScale: { value: displacementScale },
            },
            vertexShader,
            fragmentShader,
            lights: false, // Lighting custom dans fragment shader
        });
    }

    /**
     * Cre une nouvelle surface plantaire avec systme LOD multi-niveaux
     * Phase 2.3: Textures procdurales + relief topographique
     * Phase 2.2: 4 niveaux de dtail adaptatifs
     *
     * @param planetRef Rfrence plante contenant radius, seed, type, position
     */
    constructor(planetRef: PlanetReference) {
        this.planetRef = planetRef;
        this.group = new THREE.Group();
        this.group.name = `PlanetSurface_${planetRef.name}`;

        // Crer LOD group (gestion automatique niveau dtail)
        this.lodGroup = new THREE.LOD();
        this.lodGroup.name = `Planet_${planetRef.name}_LOD`;

        // Dfinition niveaux LOD (segments, distance_max)
        // Seuils calculs pour transitions invisibles (facteur <2 entre niveaux)
        const lodLevels: Array<{ segments: number; distance: number }> = [
            { segments: 128, distance: planetRef.radius * 2 },   // LOD 0: haute rsolution (proche)
            { segments: 64, distance: planetRef.radius * 5 },    // LOD 1: moyenne (normal)
            { segments: 32, distance: planetRef.radius * 10 },   // LOD 2: basse (loin)
            { segments: 16, distance: planetRef.radius * 20 },   // LOD 3: trs basse (trs loin)
        ];

        // Phase 2.3: Crer ShaderMaterial procdural (remplace MeshStandardMaterial)
        const proceduralMaterial = this.createProceduralMaterial(planetRef);
        this.materials.push(proceduralMaterial);

        // Crer chaque niveau LOD
        lodLevels.forEach((level, index) => {
            const geometry = new THREE.SphereGeometry(
                planetRef.radius,
                level.segments,  // widthSegments
                level.segments   // heightSegments
            );
            this.geometries.push(geometry);

            const mesh = new THREE.Mesh(geometry, proceduralMaterial);
            mesh.name = `Planet_${planetRef.name}_LOD${index}`;
            mesh.castShadow = true;
            mesh.receiveShadow = true;
            this.meshes.push(mesh);

            // Ajouter au LOD group avec distance seuil
            // THREE.LOD slectionne automatiquement le mesh selon distance camra
            this.lodGroup.addLevel(mesh, level.distance);
        });

        this.group.add(this.lodGroup);

        console.log(
            `[PlanetSurface] Created ${planetRef.name} with procedural textures (Phase 2.3):`,
            {
                type: planetRef.type,
                radius: planetRef.radius,
                seed: planetRef.seed,
                displacementScale: (planetRef.radius * 0.05).toFixed(2),
                lodLevels: lodLevels.length,
                lod0: `${lodLevels[0].segments}${lodLevels[0].segments} (< ${lodLevels[0].distance.toFixed(1)} units)`,
                lod1: `${lodLevels[1].segments}${lodLevels[1].segments} (< ${lodLevels[1].distance.toFixed(1)} units)`,
                lod2: `${lodLevels[2].segments}${lodLevels[2].segments} (< ${lodLevels[2].distance.toFixed(1)} units)`,
                lod3: `${lodLevels[3].segments}${lodLevels[3].segments} (< ${lodLevels[3].distance.toFixed(1)} units)`,
            }
        );
    }

    /**
     * Expose le groupe Three.js pour ajout  la scne
     * Pattern standard du projet (voir tasks.md)
     */
    public getGroup(): THREE.Group {
        return this.group;
    }

    /**
     * Retourne rfrence plante (mtadonnes)
     * Utile pour UI et debug
     */
    public getPlanetReference(): PlanetReference {
        return this.planetRef;
    }

    /**
     * Update appel chaque frame
     * Phase 2.2: Rotation simple pour visualisation
     * Phase 2.3+: Animation atmosphre, nuages, ocans
     *
     * Note: updateLOD() doit tre appel sparment avec position camra
     *
     * @param deltaTime Temps coul depuis dernire frame (secondes)
     */
    public update(deltaTime: number): void {
        // Rotation lente pour visualisation (0.1 rad/s  5.7/s)
        // Vitesse choisie pour montrer clairement la gomtrie sphrique
        this.group.rotation.y += deltaTime * 0.1;
    }

    /**
     * Met  jour le LOD selon la position de la camra
     * Doit tre appel dans la boucle principale (SceneManager.animate)
     * THREE.LOD calcule automatiquement la distance et slectionne le mesh appropri
     *
     * @param cameraPosition Position actuelle de la camra (coordonnes monde)
     */
    public updateLOD(camera: THREE.Camera): void {
        // THREE.LOD.update attend une Camera (pas une position).
        this.lodGroup.update(camera);
    }

    /**
     * Retourne le niveau LOD actuellement actif (debug)
     * Utile pour monitoring performances et validation transitions
     *
     * @returns Index LOD actif (0-3) ou -1 si aucun mesh actif
     */
    public getCurrentLODLevel(): number {
        // THREE.LOD.getCurrentLevel() retourne un index de niveau (number) dans Three.js.
        return this.lodGroup.getCurrentLevel();
    }

    /**
     * Libration mmoire (pattern Dispose obligatoire)
     * Phase 2.2: Dispose tous les niveaux LOD (gomtries + materials + meshes)
     * Voir tasks.md: "Ajouter Nouvelle Entit Three.js avec Dispose"
     */
    public dispose(): void {
        // Dispose toutes les gomtries (une par niveau LOD)
        this.geometries.forEach(geo => geo.dispose());
        this.geometries = [];

        // Dispose tous les materials (partag mais scuris)
        this.materials.forEach(mat => mat.dispose());
        this.materials = [];

        // Clear rfrences meshes
        this.meshes = [];

        // Clear groupes Three.js
        this.lodGroup.clear();
        this.group.clear();

        console.log(`[PlanetSurface] Disposed ${this.planetRef.name} (LOD system cleaned)`);
    }
}