import * as THREE from 'three';
import { CameraManager } from './CameraManager';
import { InputManager, KeyEventData, MouseEventData } from './InputManager';
import { ViewEntitiesManager } from './ViewEntitiesManager';
import { NavigationManager } from './NavigationManager';
import { StarField } from '../entities/StarField';
import { ClusterGrid } from '../entities/ClusterGrid';
import { CoordinateSystem } from '../entities/CoordinateSystem';
import { MemoryManager } from './MemoryManager';
import { UnitManager } from '../entities/UnitManager';
import { CornerUI } from '../ui/CornerUI';
import { SIMULATION_CONFIG, NAVIGATION_CONFIG } from '../config';
import { IUpdatable } from './IUpdatable';
import type { SystemReference } from '../types/NavigationState';
import { GameManager } from '../game/GameManager';

/**
 * SceneManager  Responsabilits aprs refactoring :
 * 1. Setup Three.js (scene, renderer, camera)
 * 2. Boucle animate() et propagation update() aux IUpdatable
 * 3. Coordination des transitions de vue (Enter/Escape)
 * 4. Dlgation de l'input  InputManager
 * 5. Dlgation de la gestion des entits de vue  ViewEntitiesManager
 * 6. Auto-transitions bases sur la distance camra
 */
export class SceneManager {
    // Three.js core
    private scene: THREE.Scene;
    private renderer: THREE.WebGLRenderer;
    private cameraManager: CameraManager;

    // Managers dlgus
    private navigationManager: NavigationManager;
    private inputManager: InputManager;
    private viewEntitiesManager: ViewEntitiesManager;

    // Groupe GALAXY et entits (partages, toujours prsentes)
    private galaxyGroup: THREE.Group;
    private starField: StarField;
    private clusterGrid: ClusterGrid;
    private coordinateSystem: CoordinateSystem;

    // Simulation MMO
    private memoryManager: MemoryManager;
    private unitManager: UnitManager;
    private simulationWorker: Worker;

    // Updatables et timing
    private updatables: IUpdatable[] = [];
    private lastTime: number = performance.now();

    // Auto-transitions : cooldowns et protection anti-oscillation
    private lastAutoTransitionCheck: number = 0;
    private lastViewTransitionTime: number = 0; // Timestamp de la dernire transition de vue
    private readonly VIEW_TRANSITION_GRACE_PERIOD = 1000; // 1 seconde de grce aprs chaque transition

    // Slection GALAXY (cluster coords pour priorit soleil)
    private selectedClusterCoords: { gx: number; gz: number } | null = null;

    // Raycast
    private raycaster: THREE.Raycaster;
    private mouse: THREE.Vector2;

    // UI
    private cornerUI: CornerUI;

    constructor() {
        console.log('[SceneManager] Initialisation...');

        // === Setup Three.js ===
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x000000);

        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        document.body.appendChild(this.renderer.domElement);

        // === Groupe GALAXY ===
        this.galaxyGroup = new THREE.Group();
        this.galaxyGroup.name = 'GalaxyGroup';
        this.scene.add(this.galaxyGroup);

        // === Managers ===
        this.cameraManager = new CameraManager(this.renderer.domElement);
        this.navigationManager = new NavigationManager();
        this.inputManager = new InputManager(this.renderer.domElement);
        this.viewEntitiesManager = new ViewEntitiesManager(this.scene, this.galaxyGroup);
        this.viewEntitiesManager.setCamera(this.cameraManager.getCamera());

        // === Entits GALAXY (ajoutes au galaxyGroup) ===
        this.starField = new StarField();
        this.galaxyGroup.add(this.starField.getMesh());

        this.clusterGrid = new ClusterGrid();
        this.galaxyGroup.add(this.clusterGrid.getMesh());
        this.galaxyGroup.add(this.clusterGrid.getPickGroup());

        this.coordinateSystem = new CoordinateSystem(3);
        this.galaxyGroup.add(this.coordinateSystem.getMesh());

        // === Lumires ===
        this.scene.add(new THREE.AmbientLight(0xffffff, 0.5));
        const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
        directionalLight.position.set(5, 10, 7.5);
        this.scene.add(directionalLight);

        // === Simulation MMO ===
        this.memoryManager = new MemoryManager(SIMULATION_CONFIG.maxUnits);
        this.simulationWorker = new Worker(
            new URL('./workers/SimulationWorker.ts', import.meta.url),
            { type: 'module' }
        );
        this.simulationWorker.postMessage({
            type: 'INIT',
            data: { buffer: this.memoryManager.buffer, maxUnits: this.memoryManager.maxUnits }
        });

        this.unitManager = new UnitManager(this.memoryManager);
        this.scene.add(this.unitManager.getMesh());

        // === Updatables ===
        this.updatables.push(this.starField as unknown as IUpdatable);
        this.updatables.push(this.unitManager as unknown as IUpdatable);
        this.updatables.push(this.clusterGrid as unknown as IUpdatable);
        this.updatables.push(this.coordinateSystem as unknown as IUpdatable);

        // === Raycast ===
        this.raycaster = new THREE.Raycaster();
        this.raycaster.params.Line = { threshold: 0.05 };
        this.mouse = new THREE.Vector2();

        // === UI ===
        this.cornerUI = new CornerUI();
        this.updateGridInfo();

        // === Connecter GameManager au contexte de rendu ===
        GameManager.getInstance().setRenderContext(this.scene, this.cameraManager.getCamera());

        // === Event handlers ===
        this.setupInputHandlers();
        window.addEventListener('resize', () => this.onWindowResize());
    }

    /**
     * Configure les handlers d'input via InputManager
     */
    private setupInputHandlers(): void {
        // Clavier : Enter/Escape pour transitions
        this.inputManager.on<KeyEventData>('keydown', (data) => {
            const key = data.key;
            if (key === 'Enter') this.handleEnter();
            else if (key === 'Escape') this.handleEscape();
        });

        // Clic : slection via raycast
        this.inputManager.on<MouseEventData>('click', (data) => {
            this.mouse.copy(data.position);
            this.handleClick();
        });

        // Double-clic : entre directe
        this.inputManager.on<MouseEventData>('dblclick', (data) => {
            this.mouse.copy(data.position);
            this.handleDoubleClick();
        });
        // Note: Le survol (hover) est gr dans animate() via updateHover()
        // qui utilise inputManager.mousePosition directement
    }

    private onWindowResize(): void {
        this.cameraManager.updateAspect(window.innerWidth / window.innerHeight);
        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    private updateGridInfo(): void {
        this.cornerUI.updateClusterInfo({
            clusters: 1,
            cubes: this.clusterGrid.getMesh().children.length,
            size: `${this.clusterGrid.getTotalDimensions().width}x${this.clusterGrid.getTotalDimensions().depth}`
        });
    }

    /**
     * Met  jour le hover bas sur la position actuelle de la souris
     * Appel  chaque frame dans animate()
     */
    private updateHover(): void {
        // Utilise la position NDC courante depuis InputManager
        this.mouse.copy(this.inputManager.mousePosition);
        this.raycaster.setFromCamera(this.mouse, this.cameraManager.getCamera());
        const pickObjects = this.clusterGrid.getPickObjects();
        const intersects = this.raycaster.intersectObjects(pickObjects, false);
        const name = intersects.length > 0 ? (intersects[0].object as THREE.Object3D & { name?: string }).name || null : null;
        this.clusterGrid.setHoverCubeByName(name);
    }

    // ========== HANDLERS ENTER / ESCAPE ==========

    /**
     * Refactored method to delegate view transitions to NavigationManager.
     */
    private handleEnter(): void {
        if (this.cameraManager.isTransitioning()) {
            console.log('[SceneManager] handleEnter() blocked: camera transition in progress');
            return;
        }

        const navState = this.navigationManager.getState();
        if (navState.currentView === 'GALAXY' && navState.currentSystem) {
            this.navigationManager.enterSystem(navState.currentSystem);
        } else {
            console.warn('[SceneManager] handleEnter() called in invalid state:', navState);
        }
    }

    private handleEscape(): void {
        if (this.cameraManager.isTransitioning()) {
            console.log('[SceneManager] handleEscape() blocked: camera transition in progress');
            return;
        }

        const navState = this.navigationManager.getState();
        if (navState.currentView === 'SYSTEM') {
            this.navigationManager.setViewMode('GALAXY');
        } else {
            console.warn('[SceneManager] handleEscape() called in invalid state:', navState);
        }
    }

    private enterSystem(systemRef: SystemReference): void {
        const meta = systemRef.metadata;
        
        // Validation de la SystemReference
        if (!meta) {
            console.error('[SceneManager] enterSystem() ERREUR : metadata manquante dans systemRef');
            return;
        }
        
        // Position cible : prfrer sunMesh si disponible, sinon fallback sur metadata
        const hasSunMesh = !!systemRef.sunMesh;
        const targetPos = hasSunMesh
            ? systemRef.sunMesh!.position.clone()
            : new THREE.Vector3(meta.absolutePosition.x, meta.absolutePosition.y, meta.absolutePosition.z);
        
        console.log('[SceneManager] enterSystem()', {
            systemName: meta.name,
            systemId: meta.id,
            hasSunMesh,
            targetPos: { x: targetPos.x.toFixed(3), y: targetPos.y.toFixed(3), z: targetPos.z.toFixed(3) },
            optimalDistance: meta.optimalDistance
        });
        
        const entryDistance = meta.optimalDistance * NAVIGATION_CONFIG.systemEnterDistanceFactor;
        
        // Marquer le dbut de la transition de vue
        this.lastViewTransitionTime = performance.now();

        // Utiliser useStandardAngle=true pour centrer parfaitement le soleil  l'cran
        // lors de la transition vers la vue SYSTEM
        this.cameraManager.flyTo(targetPos, entryDistance, () => {
            // Marquer la fin de la transition (reset le timer pour la priode de grce)
            this.lastViewTransitionTime = performance.now();
            
            this.navigationManager.enterSystem(systemRef);
            this.viewEntitiesManager.setView('SYSTEM', this.navigationManager.getState());
            
            // Garantir le centrage du soleil aprs cration du SolarSystem
            // Le soleil du SolarSystem est  l'origine (0,0,0) du groupe
            const solarSystem = this.viewEntitiesManager.getSolarSystem();
            if (solarSystem) {
                const sunPosition = solarSystem.getSunMesh().position.clone();
                // Convertir en coordonnes monde si le groupe est positionn
                solarSystem.getGroup().localToWorld(sunPosition);
                this.cameraManager.setTarget(sunPosition);
                
                // Ajouter un grand repre 3D (AxesHelper)  l'origine du systme solaire pour debug
                // Rouge = X, Vert = Y, Bleu = Z
                const axesHelper = new THREE.AxesHelper(2.0); // Taille 2.0 units (visible par rapport au soleil)
                axesHelper.position.set(0, 0, 0); // Origine du systme solaire
                solarSystem.getGroup().add(axesHelper);
                console.log('[SceneManager] AxesHelper ajout  l\'origine du SolarSystem (0,0,0)');
            }
            
            this.cornerUI.logMessage(` Entre dans le systme ${meta.name}`);
            console.log('[SceneManager] Transition vers SYSTEM termine', {
                systemName: meta.name,
                cameraDistance: this.cameraManager.distance.toFixed(3),
                optimalDistance: meta.optimalDistance.toFixed(3)
            });
        }, true); // useStandardAngle = true pour un centrage parfait
    }

    private enterPlanet(): void {
        console.log('[SceneManager] enterPlanet() appel');
        
        const navState = this.navigationManager.getState();
        if (!navState.currentPlanet) {
            console.warn('[SceneManager] enterPlanet() : pas de plante slectionne');
            return;
        }

        const solarSystem = this.viewEntitiesManager.getSolarSystem();
        if (!solarSystem) {
            console.error('[SceneManager] enterPlanet() : SolarSystem non disponible');
            return;
        }

        const planets = solarSystem.getPlanets();
        console.log('[SceneManager] enterPlanet() recherche plante', {
            searchId: navState.currentPlanet.id,
            availablePlanets: planets.map(p => p.id)
        });
        
        const planet = planets.find(p => p.id === navState.currentPlanet!.id);
        if (!planet) {
            console.error('[SceneManager] enterPlanet() : plante non trouve dans SolarSystem');
            return;
        }

        const targetPos = planet.mesh.position.clone();
        const entryDistance = navState.currentPlanet.radius * 2.5;
        
        console.log('[SceneManager] enterPlanet() dmarrage flyTo', {
            planetId: planet.id,
            targetPos: { x: targetPos.x.toFixed(2), y: targetPos.y.toFixed(2), z: targetPos.z.toFixed(2) },
            entryDistance: entryDistance.toFixed(2)
        });
        
        // Marquer le dbut de la transition de vue
        this.lastViewTransitionTime = performance.now();

        this.cameraManager.flyTo(targetPos, entryDistance, () => {
            console.log('[SceneManager] enterPlanet() transition termine');
            
            // Marquer la fin de la transition
            this.lastViewTransitionTime = performance.now();
            
            this.navigationManager.enterPlanet(navState.currentPlanet!);
            this.viewEntitiesManager.setView('PLANET', this.navigationManager.getState());
            this.cornerUI.logMessage(` Entre sur la plante ${navState.currentPlanet!.id}`);
        });
    }

    private exitPlanet(): void {
        const navState = this.navigationManager.getState();
        if (!navState.currentPlanet) return;

        const planetRef = navState.currentPlanet;
        
        // Marquer la transition de vue
        this.lastViewTransitionTime = performance.now();
        
        this.navigationManager.exitPlanet();
        this.viewEntitiesManager.setView('SYSTEM', this.navigationManager.getState());

        const targetPos = new THREE.Vector3(planetRef.position.x, planetRef.position.y, planetRef.position.z);
        const exitDistance = planetRef.radius * 2.5;
        const direction = new THREE.Vector3().subVectors(this.cameraManager.getCamera().position, targetPos).normalize();
        if (direction.lengthSq() < 0.0001) direction.set(0, 0, 1);

        this.cameraManager.setTarget(targetPos);
        this.cameraManager.setPosition(targetPos.x + direction.x * exitDistance, targetPos.y + direction.y * exitDistance, targetPos.z + direction.z * exitDistance);
        this.cornerUI.logMessage(` Retour au systme depuis ${planetRef.id}`);
    }

    public exitSystem(): void {
        // Marquer la transition de vue
        this.lastViewTransitionTime = performance.now();
        
        const exitedSystem = this.navigationManager.exitSystem();
        if (!exitedSystem) return;

        this.viewEntitiesManager.setView('GALAXY', this.navigationManager.getState());

        const meta = exitedSystem.metadata;
        const targetPos = new THREE.Vector3(meta.absolutePosition.x, meta.absolutePosition.y, meta.absolutePosition.z);
        const direction = new THREE.Vector3().subVectors(this.cameraManager.getCamera().position, targetPos).normalize();
        if (direction.lengthSq() < 0.0001) direction.set(0, 0, 1);

        const distance = meta.optimalDistance * NAVIGATION_CONFIG.systemExitDistanceFactor;
        const newPos = targetPos.clone().add(direction.multiplyScalar(distance));

        this.cameraManager.setTarget(targetPos);
        this.cameraManager.setPosition(newPos.x, newPos.y, newPos.z);
        this.cornerUI.logMessage(` Sortie du systme ${meta.name}`);
        
        console.log('[SceneManager] Sortie systme effectue', {
            systemName: meta.name,
            newCameraDistance: this.cameraManager.distance.toFixed(3)
        });
    }

    // ========== CLICK HANDLERS ==========

    private handleClick(): void {
        this.raycaster.setFromCamera(this.mouse, this.cameraManager.getCamera());
        const navState = this.navigationManager.getState();

        if (navState.currentView === 'SYSTEM') {
            this.handleSystemClick();
        } else {
            this.handleGalaxyClick();
        }
    }

    /**
     * Gre le clic en vue SYSTEM : slection de plante (sans changer de vue).
     * La transition vers la vue PLANET se fait via Enter ou double-clic.
     */
    private handleSystemClick(): void {
        const solarSystem = this.viewEntitiesManager.getSolarSystem();
        if (!solarSystem) return;

        const planetMeshes = solarSystem.getPlanets().map(p => p.mesh);
        const intersects = this.raycaster.intersectObjects(planetMeshes, false);

        if (intersects.length > 0) {
            const hit = intersects[0].object as THREE.Mesh;
            const planetId = hit.userData.planetId as string | undefined;
            if (planetId) {
                // Affichage visuel de la slection (anneau)
                solarSystem.selectPlanet(planetId, true);
                
                const planet = solarSystem.getPlanets().find(p => p.id === planetId);
                if (planet) {
                    // Construire la rfrence plante
                    const planetRef = {
                        id: planet.id,
                        name: planet.id,
                        radius: planet.radius,
                        position: {
                            x: planet.mesh.position.x,
                            y: planet.mesh.position.y,
                            z: planet.mesh.position.z
                        },
                        seed: 0,
                        type: 'telluric' as const
                    };
                    
                    // CORRECTION: Slectionner la plante SANS changer de vue
                    // (comme setGalaxySelection pour les systmes)
                    this.navigationManager.setSystemSelection(planetRef);
                    
                    // Centrer la camra sur la plante
                    this.cameraManager.setTarget(planet.mesh.position);
                    
                    this.cornerUI.logMessage(` Plante slectionne: ${planetRef.name} (Enter pour entrer)`);
                }
            }
        } else {
            // Clic dans le vide : dslectionner
            solarSystem.selectPlanet('', false);
            this.navigationManager.setSystemSelection(null);
        }
    }

    private handleGalaxyClick(): void {
        const pickObjects = this.clusterGrid.getPickObjects();
        const cubeIntersects = this.raycaster.intersectObjects(pickObjects, false);
        const sunIntersects = this.raycaster.intersectObjects(this.clusterGrid.getSuns(), false);

        const cubeHit = cubeIntersects.length > 0 ? cubeIntersects[0] : null;
        const sunHit = sunIntersects.length > 0 ? sunIntersects[0] : null;
        const cubeName = cubeHit ? ((cubeHit.object as THREE.Object3D & { name?: string }).name || null) : null;
        const clickedSun = sunHit ? (sunHit.object as THREE.Mesh) : null;
        const sunMetadata = clickedSun ? this.clusterGrid.getSunMetadataFromMesh(clickedSun) : null;

        // Rgle de priorit soleil
        const shouldSelectSun = (() => {
            if (!clickedSun) return false;
            if (!this.selectedClusterCoords) return true;
            const sunCoords = sunMetadata?.globalCoords || null;
            if (!sunCoords) return false;
            const clusterDistance = Math.max(Math.abs(this.selectedClusterCoords.gx - sunCoords.gx), Math.abs(this.selectedClusterCoords.gz - sunCoords.gz));
            return clusterDistance <= 1;
        })();

        if (shouldSelectSun && sunMetadata && clickedSun) {
            this.clusterGrid.selectSun(clickedSun);
            this.clusterGrid.selectCubeByName(null);
            this.selectedClusterCoords = null;
            this.cornerUI.updateSelectedCluster(null, null);
            this.cornerUI.updateSelectedSun(sunMetadata);
            this.cornerUI.logMessage(` Soleil slectionn: ${sunMetadata.name}`);
            this.cameraManager.setTarget(clickedSun.position);
            
            // Construction de la SystemReference
            // Prfrer getSystemReference() pour cohrence, sinon crer un fallback valide
            let systemRef = this.clusterGrid.getSystemReference(sunMetadata.id);
            if (!systemRef) {
                console.warn('[SceneManager] getSystemReference() retourne null pour', sunMetadata.id, ' utilisation du fallback');
                systemRef = {
                    metadata: sunMetadata,
                    sunMesh: clickedSun,
                    pickMesh: clickedSun
                };
            }
            
            console.log('[SceneManager] handleGalaxyClick()  setGalaxySelection', {
                systemId: systemRef.metadata.id,
                systemName: systemRef.metadata.name,
                hasSunMesh: !!systemRef.sunMesh,
                sunMeshPosition: systemRef.sunMesh ? {
                    x: systemRef.sunMesh.position.x.toFixed(3),
                    y: systemRef.sunMesh.position.y.toFixed(3),
                    z: systemRef.sunMesh.position.z.toFixed(3)
                } : 'N/A'
            });
            
            this.navigationManager.setGalaxySelection(systemRef);
        } else if (cubeName) {
            const globalCoords = this.clusterGrid.getGlobalCoordsFromName(cubeName);
            this.clusterGrid.selectCubeByName(cubeName);
            this.clusterGrid.selectSun(null);
            this.cornerUI.updateSelectedSun(null);
            this.cornerUI.updateSelectedCluster(cubeName, globalCoords);
            this.selectedClusterCoords = globalCoords;
            this.navigationManager.setGalaxySelection(null);
        } else {
            this.clusterGrid.selectCubeByName(null);
            this.clusterGrid.selectSun(null);
            this.cornerUI.updateSelectedCluster(null, null);
            this.cornerUI.updateSelectedSun(null);
            this.selectedClusterCoords = null;
            this.navigationManager.setGalaxySelection(null);
        }
    }

    private handleDoubleClick(): void {
        this.handleClick();
        this.handleEnter();
    }

    // ========== AUTO-TRANSITIONS ==========

    /**
     * Vrifie et dclenche les transitions automatiques bases sur la distance camra.
     * 
     * PROTECTION ANTI-OSCILLATION :
     * - Ne se dclenche pas pendant une transition camra (flyTo)
     * - Respecte un cooldown entre les vrifications
     * - Respecte une priode de grce aprs chaque transition de vue
     */
    private checkAutoTransitions(): void {
        const now = performance.now();
        
        // Protection 1 : Cooldown entre les vrifications
        if (now - this.lastAutoTransitionCheck < NAVIGATION_CONFIG.transitionCooldown) return;
        
        // Protection 2 : Transition camra en cours
        if (this.cameraManager.isTransitioning()) return;
        
        // Protection 3 : Priode de grce aprs une transition de vue
        // vite les oscillations GALAXYSYSTEM quand la camra vient de se repositionner
        if (now - this.lastViewTransitionTime < this.VIEW_TRANSITION_GRACE_PERIOD) {
            return;
        }

        const navState = this.navigationManager.getState();
        const currentDistance = this.cameraManager.distance;

        // GALAXY  SYSTEM (zoom in automatique)
        if (navState.currentView === 'GALAXY' && navState.currentOptimalDistance !== null && navState.currentSystem !== null) {
            const enterThreshold = navState.currentOptimalDistance * NAVIGATION_CONFIG.systemEnterDistanceFactor;
            if (currentDistance < enterThreshold) {
                console.log('[SceneManager] Auto-transition GALAXY  SYSTEM', {
                    currentDistance: currentDistance.toFixed(3),
                    enterThreshold: enterThreshold.toFixed(3)
                });
                this.lastAutoTransitionCheck = now;
                this.lastViewTransitionTime = now;
                this.navigationManager.enterSystem(navState.currentSystem);
                this.viewEntitiesManager.setView('SYSTEM', this.navigationManager.getState());
                this.cornerUI.logMessage(` Entre automatique dans ${navState.currentSystem.metadata.name}`);
                return;
            }
        }

        // SYSTEM  GALAXY (zoom out automatique)
        if (navState.currentView === 'SYSTEM' && navState.currentOptimalDistance !== null) {
            const exitThreshold = navState.currentOptimalDistance * NAVIGATION_CONFIG.systemExitDistanceFactor;
            if (currentDistance > exitThreshold) {
                console.log('[SceneManager] Auto-transition SYSTEM  GALAXY', {
                    currentDistance: currentDistance.toFixed(3),
                    exitThreshold: exitThreshold.toFixed(3),
                    optimalDistance: navState.currentOptimalDistance.toFixed(3)
                });
                this.lastAutoTransitionCheck = now;
                this.exitSystem();
                return;
            }
        }

        // SYSTEM  PLANET (zoom in sur plante slectionne)
        if (navState.currentView === 'SYSTEM' && navState.currentPlanet) {
            const solarSystem = this.viewEntitiesManager.getSolarSystem();
            if (solarSystem) {
                const planet = solarSystem.getPlanets().find(p => p.id === navState.currentPlanet!.id);
                if (planet) {
                    const planetEnterThreshold = planet.radius * NAVIGATION_CONFIG.planetEnterDistanceFactor;
                    if (currentDistance < planetEnterThreshold) {
                        this.lastAutoTransitionCheck = now;
                        this.enterPlanet();
                        return;
                    }
                }
            }
        }

        // PLANET  SYSTEM (zoom out depuis plante)
        if (navState.currentView === 'PLANET' && navState.currentPlanet) {
            const planetExitThreshold = navState.currentPlanet.radius * NAVIGATION_CONFIG.planetExitDistanceFactor;
            if (currentDistance > planetExitThreshold) {
                this.lastAutoTransitionCheck = now;
                this.exitPlanet();
            }
        }
    }

    // ========== BOUCLE PRINCIPALE ==========

    public animate(): void {
        requestAnimationFrame(() => this.animate());

        const now = performance.now();
        const dt = (now - this.lastTime) / 1000;
        this.lastTime = now;

        // Update managers
        this.cameraManager.update();
        this.viewEntitiesManager.update(dt);

        // Update GameManager (combat, conomie, units)
        GameManager.getInstance().update(dt);

        // Update updatables
        const camPos = this.cameraManager.getCamera().position;
        for (const u of this.updatables) {
            try { u.update(dt, camPos); } catch (e) { console.error('[SceneManager] update error', e); }
        }

        // Update hover (survol) bas sur la position souris courante
        this.updateHover();

        // Debug UI
        const camTarget = this.cameraManager.getTarget();
        try { this.cornerUI.updateCameraDebug({ x: camPos.x, y: camPos.y, z: camPos.z }, { x: camTarget.x, y: camTarget.y, z: camTarget.z }); } catch { /* non bloquant */ }

        // Auto-transitions (avec toutes les protections)
        this.checkAutoTransitions();

        // Render
        this.renderer.render(this.scene, this.cameraManager.getCamera());
    }

    public resetCameraToOptimal(): void {
        this.cameraManager.resetPosition();
        this.cornerUI.logMessage(' Camra rinitialise');
    }

    public getNavigationState() { return this.navigationManager.getState(); }
    public getCamera() { return this.cameraManager.getCamera(); }
    public getScene() { return this.scene; }
}